import React, { Component, Fragment } from "react";
import { withRouter } from "react-router-dom"

class ItemLot extends Component {

  onDelete = (id) => {
    this.props.onDelete(id);
  };
  getDate(timeStamp){
    return new Date(timeStamp)
  }

  render() {
    let { listLot, index } = this.props;
    let ctime=new Date(listLot.mtime)
      return (
        <Fragment>
          { listLot.product_name !== 'NA' ? 
          <tr  key={index}>
            <th scope="row">{index + 1}</th>
            <td>{listLot.code}</td>
            <td>{listLot.product_name}</td>
            <td>{listLot.warehouse_name}</td>
            <td>{listLot.total}</td>
            <td>{listLot.remain}</td>
            <td>{listLot.man_date}</td>
            <td>{listLot.exp_date}</td>
            <td>{ctime.toLocaleString('en-GB')}</td>
          </tr>
          :null }
        </Fragment>
      );
    }
}

export default withRouter(ItemLot);
