import React, { useState, useEffect } from 'react';
import { Fragment } from 'react';
import { Button, Table, FormGroup, Label, Input, Col } from 'reactstrap';
import { Link } from "react-router-dom";
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm"
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import ServiceService from "../../Shared/ServiceService"
import { SERVICE_TYPE } from "../../../../Constances/const"

const OPTION = "option";
const ServiceList = (props) => {
    const [serviceList, setServiceList] = useState([]);
    const [delServiceId, setDelServiceId] = useState(null);
    const [confirmMessage, setConfirmMessage] = useState("");
    const [notiMessage, setNotiMessage] = useState("");
    const [serviceType , setServiceType] = useState("");
    const [discountPrice , setDiscountPrice] = useState("");
    const [serviceChoice, setServiceChoice] = useState([]);
    const [flag, setFlag] = useState(false);

    useEffect(() => {{
        ServiceService.getServiceList().then(res => {
            setServiceList(res.data)
            }).catch(err => {
                setNotiMessage("C li xy ra, vui lng th li!")
            })
        }
    }, [flag])

    const handleDelService = (service) => {
        setDelServiceId(service.id)
        setConfirmMessage(`Bn c mun xa dch v ${service.name}?`)
    }

    const answer = (answer) => {
        if (answer) {
            ServiceService.delService(delServiceId).then(() => {
                setDelServiceId(null)
                setConfirmMessage("")
                setNotiMessage("Bn  cp nht thnh cng!")
                setFlag(!flag);
            }).catch((err) => {
                setNotiMessage("C li xy ra, vui lng th li!")
                console.log(err)
            })

        } else {
            setConfirmMessage("")
        }
    }

    const done = () => {
        setNotiMessage("")
    }

    const handleSelectService = (event) => {
        setServiceType(event.target.value);
    }
    
    const handleSelectDiscount = (event) => {
        setDiscountPrice(event.target.value);
    }

    const handleConfirmPrice = () => {
        if (serviceType === OPTION) {
            let data = [];
            serviceChoice.forEach(service_id => {
                serviceList.forEach(service => {
                    if (service_id == service.id) {
                        const obj = {
                            id : service.id,
                            price : (Math.ceil(service.origin_price * (1 - discountPrice * 0.01) * 0.001)) * 1000
                        }
                        data.push(obj);
                    }
                })
            })
            const promise_service = data.map(dt => ServiceService.updatePriceService(dt))
            Promise.all(promise_service).then(
                ()=> { 
                    setNotiMessage("Bn  cp nht thnh cng!");
                    setFlag(!flag);
                }).catch(err => {
                    console.log(err)
                    setNotiMessage("C li xy ra, vui lng th li!")
                }
            );
        } else {
            const data = {
                type : serviceType,
                discount : parseInt(discountPrice)
            }
            ServiceService.updatePriceDiscount(data).then(
                ()=> { 
                    setNotiMessage("Bn  cp nht thnh cng!") 
                    setFlag(!flag);
                }).catch(err => {
                    setNotiMessage("C li xy ra, vui lng th li!")
                }
            )
        }
    }
    
    const handleCheckBox = (e) => {
        if (e.target.checked == true) {
            setServiceChoice(old => [...old, e.target.value]);
        } else {
            let services = [...serviceChoice];
            services.splice(serviceChoice.indexOf(e.target.value),1);
            setServiceChoice(services);
        }
    }

    return (
        <Fragment>
            <div className="container">
                <ModalConfirm message={confirmMessage} answer={answer}></ModalConfirm>
                <ModalNoti message={notiMessage} done={done}></ModalNoti>
                <ServiceTable serviceList={serviceList} handleDelService={handleDelService} handleConfirmPrice={handleConfirmPrice} handleCheckBox={handleCheckBox}
                    handleSelectService={handleSelectService} handleSelectDiscount={handleSelectDiscount} serviceType={serviceType} serviceChoice={serviceChoice}> </ServiceTable>
            </div>
        </Fragment>
    );
}

const ServiceTable = (props) => {
    let { serviceList, handleDelService , handleSelectService, handleSelectDiscount,handleConfirmPrice , handleCheckBox, serviceType, serviceChoice} = props;
    return (
        <div className="customCard mt-3">
            <FormGroup row>
                <Col sm={6}>
                    <h4 className="title-card-lg">Qun l dch v</h4>
                    <Link to={`/app/service/0`}>
                        <Button color="primary" outline className="mb-3">Thm mi dch v</Button>
                    </Link>
                </Col>
               <Col sm={6}>
                    <FormGroup row className = "justify-content-md-end">
                        <Col sm={6}>
                            <h4 className="title-card-lg ">Chit khu dch v</h4>
                        </Col>
                    </FormGroup>
                    <FormGroup row className = "justify-content-md-end">
                        <Col sm={6}>
                            <Input type="select" name="type" id="type" onChange = {event => { handleSelectService(event) }} >
                                <option value="">Chn loi dch v</option>
                                <option value={SERVICE_TYPE.TEST}>Xt nghim</option>
                                <option value={SERVICE_TYPE.EXAM}>Khm bnh</option>
                                <option value={SERVICE_TYPE.ENT}>Ni soi</option>
                                <option value={SERVICE_TYPE.ULTRASOUND}>Siu m</option>
                                <option value={SERVICE_TYPE.XRAY}>X-Quang</option>
                                <option value={SERVICE_TYPE.OTHER}>Cp cu</option>
                                <option value={OPTION}>Ty chn DV</option>
                            </Input>
                        </Col>
                    </FormGroup>
                    <FormGroup row className = "justify-content-md-end">
                        <Col sm={6}>
                            <Input type="select" name="type" id="type" onChange = {event => { handleSelectDiscount(event) }}>
                                <option value="">Chn mc chit khu</option>
                                <option value = "0"> Gi gc</option>
                                <option value = "10"> 10%</option>
                                <option value = "20"> 20%</option>
                                <option value = "30"> 30%</option>
                                <option value = "40"> 40%</option>
                                <option value = "50"> 50%</option>
                                <option value = "60"> 60%</option>
                                <option value = "70"> 70%</option>
                                <option value = "80"> 80%</option>
                                <option value = "90"> 90%</option>
                                <option value = "100"> 100%</option>
                            </Input>
                        </Col>
                    </FormGroup> 
                    <FormGroup row className = "justify-content-md-end">
                        <Col sm = {2}>
                            <Button color="primary" outline className="m-2 " onClick = {() => { handleConfirmPrice() }}>Lu</Button>
                    </Col>
                </FormGroup>
               </Col>
                
            </FormGroup>
            
            
            <Table className="serviceListTable" hover bordered striped>
                <thead>
                    <tr>
                        <th>STT</th>
                        <th>M dch v</th>
                        <th>Tn dch v</th>
                        <th>Loi dch v</th>
                        <th className="price">Gi khuyn mi</th>
                        <th className="price">Gi gc</th>
                        <th>Thao tc</th>
                    </tr>
                </thead>
                <tbody className="bodyTable">
                    {serviceList.map((item, index) => {
                        return item.price == item.origin_price ? 
                        <tr key={item.id}>
                            <th scope="row">{index + 1}</th>
                            <td>{item.code}</td>
                            { serviceType == OPTION
                            ? <td>
                                <Input 
                                    className="mr-10 p-2" 
                                    type="checkbox" 
                                    value={item.id}
                                    // defaultChecked={ serviceChoice.find(item => item == item.id ? true : false)}
                                    onClick={(e) => { handleCheckBox(e) }}
                                />   
                                {item.name}
                            </td> : <td>{item.name}</td> }
                            <td>
                                {item.type === SERVICE_TYPE.TEST && "Xt nghim"}
                                {item.type === SERVICE_TYPE.EXAM && "Khm bnh"}
                                {item.type === SERVICE_TYPE.ULTRASOUND && "Siu m"}
                                {item.type === SERVICE_TYPE.ENT && "Ni soi"}
                                {item.type === SERVICE_TYPE.XRAY && "Chp X-Quang"}
                                {item.type === SERVICE_TYPE.OTHER&& "Cp cu"}
                            </td> 
                            <td className="price">{item.price}</td>
                            <td className="price">{item.origin_price}</td>
                            <td >
                                <Link to={`/app/service/${item.id}`}>
                                    <Button color="primary" className="btn-sm">Sa</Button> {" "}
                                </Link>
                                <Button color="danger" className=" btn-sm" onClick={() => { handleDelService(item) }}>Xa</Button>
                            </td>
                        </tr> : <tr key={item.id}>
                            <th scope="row">{index + 1}</th>
                            <td>{item.code}</td>
                            { serviceType == OPTION
                            ? <td>
                                <Input 
                                    className="mr-10 p-2" 
                                    type="checkbox" 
                                    value={item.id}
                                    defaultChecked={true}
                                    onClick={(e) => { handleCheckBox(e) }}
                                />   
                                {item.name}
                            </td> : <td>{item.name}</td> }
                            <td>
                                {item.type === SERVICE_TYPE.TEST && "Xt nghim"}
                                {item.type === SERVICE_TYPE.EXAM && "Khm bnh"}
                                {item.type === SERVICE_TYPE.ULTRASOUND && "Siu m"}
                                {item.type === SERVICE_TYPE.ENT && "Ni soi"}
                                {item.type === SERVICE_TYPE.XRAY && "Chp X-Quang"}
                                {item.type === SERVICE_TYPE.OTHER&& "Cp cu"}
                            </td>
                            <td className="price">{item.price} *</td>
                            <td className="price" style={{fontWeight : "bold"}}>{item.origin_price}</td>
                            <td >
                                <Link to={`/app/service/${item.id}`}>
                                    <Button color="primary" className="btn-sm">Sa</Button> {" "}
                                </Link>
                                <Button color="danger" className=" btn-sm" onClick={() => { handleDelService(item) }}>Xa</Button>
                            </td>
                        </tr>
                    })}
                </tbody>
            </Table>
        </div>
    );
}

export default ServiceList;

