import React, { Component, Fragment } from 'react';
import { Table, Modal, ModalHeader, ModalBody, ModalFooter, Input, Label, Col, Row } from 'reactstrap';
import { SERVICE_TYPE, STEP_TYPE, ORDER_TYPE, STATUS, ModalNoti, Util  } from '../../Shared';
class CancelTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }     
    }
    render() {
        let { items, customerCode } = this.props;
        let total = 0;
        let item = items.filter(i => i.order?.customer?.code.includes(`${customerCode}`)).map((i, index) => {
            let type = i.job_step.type === 'buy' ? i.order?.type : i.order?.items[0]?.ref_value?.type;
            total += i.order.total;
            return  <tr key={index}>
                        <td> {index+1} </td>
                        <td> {i.order?.customer?.full_name} </td>
                        <td> {i.order?.customer?.code} </td>
                        <td> {i.order?.customer?.contacts[0]?.phone} </td>
                        <td> {i.order?.customer?.contacts[0]?.address?.province} </td>
                        <td> {i.job_step.type === STEP_TYPE.BUY ? type === ORDER_TYPE.ETC.code ? ORDER_TYPE.ETC.label : type === ORDER_TYPE.OTC.code ? ORDER_TYPE.OTC.label : ORDER_TYPE.OTHER.label
                        : type === SERVICE_TYPE.EXAM ? 'Khm bnh' : type === SERVICE_TYPE.TEST ? 'Xt nghim' : type === SERVICE_TYPE.XRAY ? 'X quang'
                        : type === SERVICE_TYPE.ULTRASOUND ? 'Siu m' : type === SERVICE_TYPE.ENT ?  'Ni soi' : 'Khc'} </td>
                        <td> {i.job_step?.results?.map(r => r.reason)} </td>
                        <td>{new Date(i.job_step.ctime).toLocaleString('en-GB')}</td>
                        <td>{new Date(i.job_step.mtime).toLocaleString('en-GB')}</td>
                        <td> {Util.formatPrice(i.order.total)} </td>
                    </tr>
        })
        return (
            <Fragment>
                 <Table>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Bnh nhn</th>
                            <th>M bnh nhn</th>
                            <th>in thoi</th>
                            <th>a ch</th>
                            <th>Giao dch hy</th>
                            <th>L do</th>
                            <th>Thi gian GD</th>
                            <th>Thi gian hy</th>
                            <th>Tin</th>
                        </tr>
                    </thead>
                    <tbody>
                        {item}
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>Tng: </th>
                            <th>{Util.formatPrice(total)}</th>
                        </tr>
                    </tbody>
                </Table>
            </Fragment>
        );
    }
}

export default CancelTable;