import React, { Component } from "react";
import { Table, Container, Button } from "reactstrap";
import ItemLocation from "./ItemLocation";
import { Link } from "react-router-dom";
import LocationService from "../Shared/LocationService";
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm";

class ListLocation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      list_location: [],
      notiConfirm: "",
      list_type_location: [],
    };
  }

  componentDidMount() {
    this.getData();
  }

  // list data location
  getData() {
    let promiseArr = [];
    promiseArr.push(LocationService.listLocation());
    promiseArr.push(LocationService.listTypeLocation());
    Promise.all(promiseArr).then(([listLocation, listType]) => {
      let list_location = listLocation.data;
      let list_type_location = listType.data;
      list_location.map(lo => {
        let found = list_type_location.find(t => {
          return lo.type === t.code;
        })
        lo.location_type_name = found ? found.name : 'NA';
        return lo;
      })
      
      this.setState({
        list_location,
        list_type_location,
      });
    })
      .catch((err) => {
        this.setState({
          notiMessage: "Li vui lng bn th li sau !!",
        });
        console.log(err);
      });
  }

  onDelete = (id) => {
    this.setState({
      notiConfirm: "Bn c chc chn mun xa !!",
      id: id,
    });
  };

  answer = (isYes) => {
    if (isYes) {
      let data = {
        id: this.state.id,
      };
      LocationService.deleteLocation(data)
        .then(() => {
          this.setState({ notiConfirm: "" });
          this.getData();
          window.location.href = "./location";
        })
        .catch((err) => {
          this.setState({
            notiMessage: "Li vui lng bn th li sau !!",
          });
          console.log(err);
        });
    } else {
      this.setState({ notiConfirm: "" });
    }
  };

  render() {
    let { list_location } = this.state;
    this.state.list_location.sort((a,b) => a.code.localeCompare(b.code));
    let itemList = list_location.map((lists_location, index) => {
      return (
        <ItemLocation
          index={index}
          list_location={lists_location}
          key={index}
          onDelete={this.onDelete}
        />
      );
    });
    return (
      <Container className="customCard mr-top-20">
        <div className="titleLocation">
          <h4 className="title-card-lg">Qun L Phng Ban</h4>
        </div>
        <div className="addLocation">
          <Link to={`/app/location/create`}>
            <Button color="primary" outline>
              <span className="titleAddNew">Thm Mi</span>
            </Button>
          </Link>
        </div>
        <div className="tableManager">
          <Table className="locationTable text-center" striped bordered>
            <thead>
              <tr>
                <th>STT</th>
                <th>Loi Phng</th>
                <th>M Phng</th>
                <th>Tn Phng</th>
                <th>Thao Tc</th>
              </tr>
            </thead>
            <tbody className="bodyTable">
              {itemList}
            </tbody>
          </Table>
        </div>
        <ModalConfirm
          message={this.state.notiConfirm}
          answer={this.answer}
        ></ModalConfirm>
      </Container>
    );
  }
}

export default ListLocation;
