import React, {useState, useEffect } from 'react';
import { Fragment } from 'react';
import { Button, Form, FormGroup, Label, Input, Col } from 'reactstrap';
import { useParams } from "react-router-dom"
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm"
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import ServiceService from "../../Shared/ServiceService"
import {SERVICE_TYPE , String } from "../../../../Constances/const"

const EditServiceForm = (props) => {
    let { id } = useParams();
    const [serviceList, setServiceList] = useState([])
    useEffect(() => {{
        ServiceService.getServiceList().then(res => {
            setServiceList(res.data)
            }).catch(err => {
                setNotiMessage("C li xy ra, vui lng th li!")
            })
        }
    }, [])
    const [service, setService] = useState({
        code: "",
        name: "",
        origin_price: 0,

        type: "",
        steps: [],
    })
    const [step, setStep] = useState({
        service_id: id,
        unit: '',
        name: '',
        value: '',
        device: '',        
    })
    const [confirmMessage, setConfirmMessage] = useState("")
    const [indexDeleteStep, setIndexDeleteStep] = useState([])
    const [notiMessage, setNotiMessage] = useState("")
    const [locationSelected, setLocationSelected] = useState([])
    const [listLocation, setListLocation] = useState([])
    const [typeDone, setTypeDone] = useState(-1);

    const createCode = (services ) => {
        services.sort((a,b) => a.code.localeCompare(b.code));
        const arrCode = services.map(el => el.code);
        const codeName = arrCode[arrCode.length - 1].substr(0,2);
        const codeIndex = arrCode[arrCode.length - 1].substr(2);
        const newCodeIndex = parseInt(codeIndex) + 1;
        return codeName+String(newCodeIndex , "service");
    }

    const handleChange = (e) => {
        const { name, value } = e.target;
        if (name == "origin_price") {
            service.origin_price = e.target.value;
        }
        if (e.target.value === SERVICE_TYPE.EXAM) {
            const examService = serviceList.filter(service => service.type === SERVICE_TYPE.EXAM);
            service.code = createCode(examService);
            service.type = SERVICE_TYPE.EXAM;
        } else if (e.target.value === SERVICE_TYPE.TEST) {
            const testService = serviceList.filter(service => service.type === SERVICE_TYPE.TEST);
            service.code = createCode(testService);
            service.type = SERVICE_TYPE.TEST;
        } else if (e.target.value === SERVICE_TYPE.ENT) {
            const entService = serviceList.filter(service => service.type === SERVICE_TYPE.ENT);
            service.code = createCode(entService);
            service.type = SERVICE_TYPE.ENT;
        } else if (e.target.value === SERVICE_TYPE.XRAY) {
            const xrayService = serviceList.filter(service => service.type === SERVICE_TYPE.XRAY);
            service.code = createCode(xrayService);
            service.type = SERVICE_TYPE.XRAY;
        } else if (e.target.value === SERVICE_TYPE.ULTRASOUND){
            const ultraService = serviceList.filter(service => service.type === SERVICE_TYPE.ULTRASOUND);
            service.code = createCode(ultraService);
            service.type = SERVICE_TYPE.ULTRASOUND;
        } else if (e.target.value === SERVICE_TYPE.OTHER){
            const emerService = serviceList.filter(service => service.type === SERVICE_TYPE.OTHER);
            service.code = createCode(emerService);
            service.type = SERVICE_TYPE.OTHER;
        }
        if (service.origin_price) {
            service.origin_price = parseInt(service.origin_price);
            service["price"] = service.origin_price;
        }
        console.log(service)
        setService((service) => ({ ...service, [name]: value }));
    }

    useEffect(() => {
        if (id !== "0") {
            fetchService();
            getListLocationSelected();
        }
        getListLocation();
    }, []);

    const onCheckLocation = (locationId) => {
        const index = locationSelected.indexOf(locationId)
        if(index < 0){
            locationSelected.push(locationId)
            if(id !== "0") ServiceService.addLocationService({service_id: id, location_id: locationId})
        }else{
            locationSelected.splice(index, 1)
            if(id !== "0") ServiceService.removeLocationService({service_id: id, location_id: locationId})
        }
        setLocationSelected([...locationSelected])
    }   
    const fetchService = async () => {
        let fetchedService = await ServiceService.getServiceById(id)
        setService(fetchedService.data)
    }
    const getListLocationSelected = () => {
        ServiceService.getLocationService(id)
        .then(res => {
            const arr = res.data;
            arr.forEach(a => {
                a.type = 'old'
                locationSelected.push(a.id)
                setLocationSelected([...locationSelected])
            });
        }).catch(err => console.log(err));
    }
    const getListLocation = () => {
        ServiceService.getListLocation().then(res =>{
            setListLocation(res.data);
        }).catch(err => console.log(err));
    }

    const handleSubmitForm = () => {
        if (id === "0") {
            ServiceService.postService(service)
            .then((res) => {
                const obj = {
                    service_id: res.data.id,
                    steps: service.steps,
                }
                let promiseArr = [];
                if(service.type === SERVICE_TYPE.TEST) promiseArr.push(ServiceService.addStepService(obj))
                locationSelected.forEach(l =>{
                    const data = {
                        location_id: l,
                        service_id: res.data.id
                    }
                    promiseArr.push(ServiceService.addLocationService(data))
                })
                Promise.all(promiseArr)
                .then(resp => {
                    setNotiMessage("Thm mi dch v thnh cng!")
                    setTypeDone(0)
                }).catch(err => {
                    console.log(err)
                    setNotiMessage("C li khi thm trng d liu!")
                })   
            }).catch((err) => {
                console.log(err)
                setNotiMessage("C li xy ra, vui lng th li!")
            })
        } else {
          //cap nhat
           ServiceService.editService(service)
           .then(res => {
                let arrNew = [];
                let promiseArr = [];
                service.steps.forEach(s => {
                    if(s.type === 'new'){
                        delete s.type; 
                        arrNew.push(s);
                    }else if(s.type === 'update'){
                        delete s.type;
                        let data = {
                            id: s.id,
                            service_id: id,
                            name: s.name,
                            unit: s.unit,
                            device: s.device,
                            value: s.value
                        }
                        promiseArr.push(ServiceService.updateStepService(data))
                    }
                });
                const objNew = {
                    service_id: id,
                    steps: arrNew
                }
                promiseArr.push(ServiceService.addStepService(objNew));
                Promise.all(promiseArr)
                .then((resp) => {
                    setNotiMessage("Cp nht dch v thnh cng!")
                    setTypeDone(1)
                }).catch(err => console.log(err))
           }).catch(er => console.log(er))
        }
    }

    const answer = (answer) => {
        if (answer) {
            ServiceService.deleteStepService({id: indexDeleteStep[1]})
            .then(res => {
                service.steps.splice(indexDeleteStep[0], 1);
                setService({...service});
                indexDeleteStep.splice(0,2);
                setIndexDeleteStep(indexDeleteStep)
                setConfirmMessage("")
                setNotiMessage("Xa thnh cng!")
            }).catch(err => {
                console.log(err);
                setNotiMessage("Xa tht bi!")
            })    
        } else {
            setConfirmMessage("")
            indexDeleteStep.splice(0,2);
            setIndexDeleteStep(indexDeleteStep)
        }
    }
    const onDeleteStep = (e, i, id) => {
        indexDeleteStep.push(i, id)
        setIndexDeleteStep(indexDeleteStep)
        setConfirmMessage("Bn chc chn mun xa!")
    }   
    const done = () => {
        setNotiMessage("")
        if(typeDone === 0){
            setService({
                code: "",
                name: "",
                origin_price: 0,
                type: "",
                steps: [],
            })
            setStep({
                service_id: id,
                unit: '',
                name: '',
                value: '',
                device: '',  
            })
            setLocationSelected([])
        }
        if(typeDone === 1){
            window.history.back()
        }
    }
    const onAddStep = () => {
        if(step.name){
            step.type = 'new'
            service.steps.push(step)
            setService({...service})
            setStep({
                service_id: id,
                unit: '',
                name: '',
                value: '',
                device: '',
            })
        }else{
            setNotiMessage("Tn trng liu khng c  trng!")
        }
    }
    const onChangeStep = (e) => {
        const { name, value } = e.target;
        setStep((step) => ({...step, [name]: value}))
    }
    // const onChangeOldStep = (e,i) => {
    //     const { name , value } = e.target;
    //     console.log(service.steps[i])
    //     service.steps[i].[name] = value;
    //     if(service.steps[i].type !== 'new'){
    //         service.steps[i].type = 'update'
    //     }
    //     setService({...service})
    // }
    return (
        <Fragment>
            <div className="container">
                <ModalConfirm message={confirmMessage} answer={answer}></ModalConfirm>
                <ModalNoti message={notiMessage} done={done}></ModalNoti>
                <h4 className="title-card-lg">{id !== "0" ? "Chnh sa dch v" : "Thm mi dch v"}</h4>
                <Form>
                    <FormGroup row>
                        <Label for="name" sm={2}>Tn dch v</Label>
                        <Col sm={10}>
                            <Input type="email" name="name" id="name" placeholder="Nhp tn dch v" value={service.name} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup>
                    {/* <FormGroup row>
                        <Label for="code" sm={2}>M dch v</Label>
                        <Col sm={10}>
                            <Input type="text" name="code" id="code" placeholder="Nhp m dch v" value={service.code} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup> */}
                    <FormGroup row>
                        <Label for="price" sm={2}>Gi gc</Label>
                        <Col sm={10}>
                            <Input type="text" name="origin_price" id="origin_price" placeholder="Nhp gi dch v" value={service.origin_price} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Label for="type" sm={2}>Loi dch v</Label>
                        <Col sm={10}>
                            <Input type="select" name="type" id="type" value={service.type} onChange={(e) => handleChange(e)} >
                                <option disabled value="">Chn loi dch v</option>
                                <option value={SERVICE_TYPE.TEST}>Xt nghim</option>
                                <option value={SERVICE_TYPE.EXAM}>Khm bnh</option>
                                <option value={SERVICE_TYPE.ENT}>Ni soi</option>
                                <option value={SERVICE_TYPE.ULTRASOUND}>Siu m</option>
                                <option value={SERVICE_TYPE.XRAY}>X-Quang</option>
                                <option value={SERVICE_TYPE.OTHER}>Cp cu</option>
                            </Input>
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Label sm={2}>Chn phng</Label>
                        <Col sm={10} className="checkbox-location">
                            {listLocation.map(l => {
                               return <Button onClick={() => onCheckLocation(l.id)} active={locationSelected.includes(l.id)}>{l.name}</Button>
                            })}
                        </Col>
                    </FormGroup>
                    {/* {service.type === SERVICE_TYPE.TEST ?
                    <FormGroup row>
                        <Label sm={2}>Thm trng d liu</Label>
                        <Col sm={3}>
                            <Input type="text" placeholder="Nhp tn trng" value={step.name} onChange={e => onChangeStep(e)} name="name"/>
                        </Col>
                        <Col sm={2}>
                            <Input type="text" placeholder="Nhp n v" value={step.unit} onChange={e => onChangeStep(e)} name="unit"/>
                        </Col>
                        <Col sm={2}>
                            <Input type="text" placeholder="Nhp ch s" value={step.value} onChange={e => onChangeStep(e)} name="value"/>
                        </Col>
                        <Col sm={2}>
                            <Input type="text" placeholder="Nhp my xt nghim" value={step.device} onChange={e => onChangeStep(e)} name="device"/>
                        </Col>
                        <Col>
                            <Button onClick={onAddStep}>Lu</Button>
                        </Col>
                    </FormGroup> : null} */}
                    {/* {service.type === SERVICE_TYPE.TEST ? service.steps.map((s,i) => {
                       return <FormGroup row key={i}>
                                <Label sm={2}></Label>
                                <Col sm={3}>
                                    <Input type="text" placeholder="Nhp tn trng" value={s.name} name="name" onChange={e => onChangeOldStep(e, i)}/>
                                </Col>
                                <Col sm={2}>
                                    <Input type="text" placeholder="Nhp n v" name="unit" value={s.unit} onChange={e => onChangeOldStep(e, i)}/>
                                </Col>
                                <Col sm={2}>
                                    <Input type="text" placeholder="Nhp ch s" value={s.value} name="value" onChange={e => onChangeOldStep(e, i)}/>
                                </Col>
                                <Col sm={2}>
                                    <Input type="text" placeholder="Nhp my xt nghim" value={s.device} name="device" onChange={e => onChangeOldStep(e, i)}/>
                                </Col>
                                <Col>
                                    <Button color="danger" onClick={e => onDeleteStep(e, i, s.id)}>Xa</Button>
                                </Col>
                            </FormGroup>
                    }) : null} */}
                    
                    
                    {/* <Row>
                        <Col sm={{ size: 10, offset: 2 }}> */}
                        <div className="addControl pb-50">
                            {/* <Link to="/app/service">
                                <Button color="danger" className="mr-2">Quay li</Button>
                            </Link> */}
                            {' '}<Button color="primary" outline onClick={() => handleSubmitForm()}>{id !== "0" ? "Cp nht" : "Thm mi"}</Button>
                        </div>
                        {/* </Col>
                    </Row> */}

                </Form>
            </div>
        </Fragment>
    );
}

export default EditServiceForm;

