import React, { Component } from "react";
import { Table, Input } from "reactstrap";
import SharedService from '../../Services/SharedService';
import { STATUS, STEP_TYPE } from '../../../Constances/const'
import { Util } from '../../../Helper/Util'
import { Button } from "bootstrap";
import ExamService from '../../../Modules/Exam/Shared/ExamService'
import PrintBarcode from "../../../Modules/Exam/Components/ExamResultPaper/CustomerFromPrint"
class PatientList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedJob: -1,
      patientList: [],
      textFilter: '',
      tabs: "1",
      comboTest: [],
      customerSearch: {},
      queryCustomer: '',
      showComboTest: false,
      currentPatient: {}
    }
  }
  interval = null;
  setShowComboTest = (showComboTest) => {
    this.setState({ showComboTest: true })
  }

  fetchPatientList = () => {
    let { status, type, location_id, resultList, hasNoti } = this.props;
    status = status.join(',');
    SharedService.getListJobStep({ location_id, status }).then((res) => {
      let cusList = res.data.filter(el => el.type === type).reverse()
      for(let i=0;i<cusList.length;i++){
        let cusList2 = cusList[i].order.items.filter(eb=>{
          return eb.quantity != 0
        })
        if(cusList2.length==0){
          cusList.splice(i,1)
          i--
        }
      }
      let queryCustomer = '';
      if (hasNoti) {
        cusList.map(c => {
          if (c.status === STATUS.RUNNING) {
            if (queryCustomer.length > 0) queryCustomer += ',' + c.order.customer_id;
            else queryCustomer += c.order.customer_id;
          }
          resultList.forEach(r => {
            if (r.customerId === c.job_ref_id) {
              if (c.seen && !r.seen) c.seen = false
              else c.seen = r.seen
            }
          })
          return c;
        });
      }
      this.setState({
        patientList: cusList,
        queryCustomer
      });
      if (hasNoti) this.getResult();
    }).catch(err => {
      console.log(err)
    })
  }

  getResult = () => {
    const customer_id = this.state.queryCustomer;
    const date = new Date().toISOString().slice(0, 10);
    const data = {
      customer_id,
      date
    }
    if (customer_id.length > 0) {
      SharedService.getResult(data).then(res => {
        let data = res.data;
        let resultList = this.props.resultList;
        data.forEach(d => {
          if (resultList.length === 0) {
            d.steps.forEach(s => {
              if(s.type !== STEP_TYPE.EXAM && s.type !== STEP_TYPE.BUY && s.status === STATUS.DONE){
                let obj = {
                  jobId: s.job_id,
                  customerId: s.job_ref_id,
                  orderId: s.order_id,
                  seen: false
                }
                resultList.push(obj)
              }
            })
          } else {
            if(d.steps[0]?.type === STEP_TYPE.EXAM) {
              d.steps.forEach(s => {
                if(s.type !== STEP_TYPE.EXAM && s.type !== STEP_TYPE.BUY  && s.status === STATUS.DONE){
                  let count = 0;
                  resultList.map(r => {
                    if (s.order_id === r.orderId) count++
                  })
                  if (count === 0) {
                    let obj = {
                      jobId: s.job_id,
                      customerId: s.job_ref_id,
                      orderId: s.order_id,
                      seen: false
                    }
                    resultList.push(obj)
                  }
                }
              })
            } 
          }
        })
        this.countResult();
      })
    }
  }
  
  countResult = () => {
    let { patientList } = this.state
    let resultList = this.props.resultList;
    let uniqueJobId = [];
    resultList.map(r => {
      patientList.map(p => {
        if (r.customerId === p.job_ref_id) {
          if (p.seen && !r.seen) p.seen = false
          else p.seen = r.seen
        }
        return p;
      })
    });

    this.setState({ patientList })
    uniqueJobId = resultList.filter(r => {
      return uniqueJobId.includes(r.jobId) ? null : uniqueJobId.push(r.jobId);
    });
    this.props.setNotiQuantity(uniqueJobId.length)
  }

  onSelectJobStep = (jobStep) => {
    if (Object.entries(this.state.currentPatient).length > 0 && jobStep.id == this.state.currentPatient.id) {
      this.setState({ 
        currentPatient: {} 
      })
      this.props.onSelectJobStep({});
    } else {
      this.setState({ currentPatient: jobStep })
      this.props.onSelectJobStep(jobStep);
    }
    if (jobStep) {
      let comboTest = jobStep.order.items.map(e => {
        if (e.quantity == 0) {
          return undefined
        } else {
          return e.ref_value.name
        }
      }).filter(e => e != undefined)
      this.setState({ comboTest: comboTest })
    }
    this.setState({
      selectedJob: jobStep.id,
      textFilter: "",
      tabs: "1",
    });
    if(this.props.hiddenPatientList) this.props.hiddenPatientList();
  }

  componentDidMount() {
    this.props.action.refresh = this.fetchPatientList;
    this.fetchPatientList();
    
    this.interval = setInterval(() => {
      this.fetchPatientList();
    }, 30000);
  }
  
  makeNewTestId = () => {
    if (this.props.ref_id) {
      ExamService.getIdByRef(this.props.ref_id).then(res => {
        let TestId = res.data.id
        this.setState({ TestId: TestId })
      })
    }
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  onFilterChange = (ev) => {
    this.setState({
      textFilter: ev.target.value
    })

  }

  sortByName = (a, b) => {
    let nameA = a.toUpperCase();
    let nameB = b.toUpperCase();
    if (nameA < nameB) return -1;
    if (nameA > nameB) return 1;
    return 0;
  }

  onSelectBarcode(e){
    const {examEditState, examEditSetState} = this.props;
    let {TestId, resultsExam, nameDevice} = examEditState;
    let {sample_id, device} = resultsExam[e.target.value];
    console.log("onSelectBarcode",TestId,sample_id )
    if(examEditState.activeTab==="3"){
      if(e.target.checked){
        TestId.push(sample_id);
        nameDevice.push(device);
      }
      else{
        TestId.splice(TestId.indexOf(sample_id),1)
        nameDevice.splice(nameDevice.indexOf(device));
      }
      console.log(TestId,"onSelectBarcode")
      examEditSetState({
        TestId: TestId,
        nameDevice: nameDevice
      })
    }
  }

  render() {
    let { patientList, tabs, textFilter, customerSearch, comboTest } = this.state;
    let { mode, examEditState, checkSelectBarcode } = this.props;
    let cusArr = customerSearch ? customerSearch.steps ? customerSearch.steps : [] : [];
    let modeList = patientList.sort((a, b) => this.sortByName(b.status, a.status))
      .filter(p => p.order.customer.code.search(textFilter) >= 0)
      .map((p, index) => {
        return (
          <tr onClick={(e) => this.onSelectJobStep(p)} className={`pointer ${(this.state.selectedJob == p.id )&& examEditState &&examEditState.resultsExam.length? ' active ' : ''}
          ${p.seen === false ? 'notiResult': ''}`} // khng sa
          key={p.id} >
            <td className="dw-10">
              {index + 1}
            </td>
            <td>{p.order.customer.code}</td>
            <td className="dw-60">
              <div>
                {p.order.customer.full_name}
              </div>
            </td>
            <td>{p.order.customer.gender == "male" ? "nam" : "n"}</td>
            <td>{new Date(p.order.customer.birthday).toLocaleString('en-GB').slice(0, 10)}</td>
            <td>{p.order.customer.contacts[0].address.province}
            </td>
            <td>{p.status === STATUS.RUNNING ? p.seen === false || p.seen ? " C KT QU" : "ANG XT NGHIM" : "CH KHM"}</td>
          </tr>
        );
      })
    return (
      <div className="patientList ">
        <div className="title-card mb-5"><span className="material-icons">people</span> {this.props.titleOfList}</div>
        <div className="patientList-search mb-3">
          <Input
             maxLength="12"
            autoFocus
            style={{ 'fontFamily': 'Arial, Material Icons' }}
            type="text"
            placeholder="&#xe8b6; Lc theo m..."
            value={textFilter}
            onChange={(ev) => this.onFilterChange(ev)}></Input>
        </div>
        <div className="tableFixHead">
          {mode === "doctor" && (
            <div className="table-responsive min-h-60 df-h-90">
              <table className="table table-head-fixed table-bordered"
                onKeyDown={(e) => Util.onKeyDown(e)}
                data-index="1">
                <thead>
                  <tr>
                    <th className="dw-5">STT</th>
                    <th className="dw-15">M bnh nhn</th>
                    <th className="dw-60">Bnh nhn</th>
                    <th>Gii tnh</th>
                    <th>Ngy sinh</th>
                    <th>a ch</th>
                    <th>Trng thi</th>
                  </tr>

                </thead>
                <tbody className="body-half-screen">
                  {modeList}
                </tbody>
              </table>
            </div>
          )}
          {mode === "exam" && (
            <div className="table-responsive min-h-60 df-h-61">
              <table className="table table-head-fixed table-bordered">
                <thead>
                  <tr>
                    <th className="dw-3">STT</th>
                    <th>M bnh nhn</th>
                    <th className="dw-40">Bnh nhn</th>
                    <th>Tn Xt nghim</th>
                  </tr>
                </thead>
                <tbody className="bodyTable">
                  {patientList ? (
                    patientList.filter(p => p.order.customer.code.search(textFilter) >= 0).map((p, index) => {
                      return (
                        <>
                          <tr onClick={(e)=>this.onSelectJobStep(p)} className={`pointer${(p.id == this.state.currentPatient.id)&&examEditState.resultsExam.length ? ' active' : ''}`} key={p.id} >
                            <td className="dw-3">
                              {index + 1}
                            </td>
                            <td>{p.order.customer.code}</td>
                            <td className="dw-40">
                              <div>
                                {p.order.customer.full_name}
                              </div>
                            </td>
                            <td className="status-cus">
                              <p title={comboTest}
                              >ComBo xt nghim</p>
                            </td>
                          </tr>
                          {examEditState.resultsExam.length?examEditState.resultsExam.map((item, index1) => {
                            return (
                              <tr className={p.id == this.state.currentPatient.id ? "active" : "inactive"}>
                                <td colSpan='1'>{index + 1}.{index1 + 1}</td>
                                {checkSelectBarcode?<td colSpan="2">{item.device}</td>:<td colSpan="3">{item.device}</td>}
                                {checkSelectBarcode?<td colSpan='1'><input onChange={(e)=>this.onSelectBarcode(e)} name={item.device} value={index1} type="checkbox"/></td>:''}
                              </tr>
                            )
                          }):''}
                        </>
                      );
                    })

                  ) : (
                    <span></span>
                  )}

                </tbody>
              </table>

            </div>)}
          {mode === "examDone" && (
            <div className="table-responsive min-h-60 df-h-61">
              <table className="table table-head-fixed table-bordered">
                <thead>
                  <tr>
                    <th className="dw-3">STT</th>
                    <th>M bnh nhn</th>
                    <th className="dw-40">Bnh nhn</th>
                    <th>Tn Xt nghim</th>
                  </tr>
                </thead>
                <tbody className="bodyTable">
                  {patientList ? (
                    patientList.filter(p => p.order.customer.code.search(textFilter) >= 0).map((p, index) => {
                      return (
                        <>
                          <tr onClick={(e) => this.onSelectJobStep(p)} className={`pointer${(this.state.selectedJob == p.id)&&examEditState.resultsExam.length ? ' active' : ''}`} key={p.id} >
                            <td className="dw-3">
                              {index + 1}
                            </td>
                            <td>{p.order.customer.code}</td>
                            <td className="dw-40">
                              <div>
                                {p.order.customer.full_name}
                              </div>
                            </td>
                            <td className="status-cus">
                              <p title={comboTest}
                              >ComBo xt nghim</p>
                            </td>
                          </tr>
                          {comboTest && comboTest.map((item, index1) => {
                            return (
                              <tr className={p.id == this.state.currentPatient.id ? "active" : "inactive"}>
                                <td colSpan='1'>{index + 1}.{index1 + 1}</td>
                                <td colSpan="3">{item}</td>
                              </tr>
                            )
                          })}
                        </>
                      );
                    })

                  ) : (
                    <span></span>
                  )}

                </tbody>
              </table>

            </div>)}

          {mode === "other" && (
            <div className="table-responsive min-h-60 df-h-60">
              <table className="table table-head-fixed table-bordered">
                <thead>
                  <tr>
                    <th className="dw-10">STT</th>
                    <td>M bnh nhn</td>
                    <th>Bnh nhn</th>
                  </tr>
                </thead>
                <tbody className="bodyTable">
                  {patientList ? (
                    patientList.filter(p => p.order.customer.code.search(textFilter) >= 0).map((p, index) => {
                      return (
                        <tr onClick={(e) => this.onSelectJobStep(p)} className={`pointer${(this.state.selectedJob == p.id)}`} key={p.id} >
                          <td className="dw-10">
                            {index + 1}
                          </td>
                          <td>{p.order.customer.code}</td>
                          <td className="dw-40">
                            <div >
                              {p.order.customer.full_name}
                            </div>
                          </td>
                        </tr>
                      );
                    })
                  ) : (
                    <span></span>
                  )}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default PatientList;
