import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Input, Label, Col, Row } from 'reactstrap';

class CancelModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            reason: ''
        }
    }
    render() {
        let { openCancel, answer } = this.props;
        return (
            <div className="CancelModal">
                <Modal isOpen={openCancel} >
                    <ModalHeader >Hy giao dch</ModalHeader>
                    <ModalBody>
                        <Row>
                            <Label xs="1">L do hy: </Label>
                            <Col xs="11"><Input type="text" value={this.state.reason} onChange={e => this.setState({reason: e.target.value})} /></Col>
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button onClick={() => { answer(this.state.reason) }}>Xc nhn</Button>{' '}
                        <Button color="danger" onClick={() => { answer() }}>Hy</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}

export default CancelModal;