import React, { useState, useEffect } from 'react';
import { Fragment } from 'react';
import { Button, Table} from 'reactstrap';
import { Link } from "react-router-dom";
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm"
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import PricePolicyService from "../../Shared/PricePolicyService"

const PolicyPriceManager = (props) => {
    const [policyPriceList, setPolicyPriceList] = useState([])
    const [delPolicyPriceId, setDelPolicyPriceId] = useState(null)
    const [confirmMessage, setConfirmMessage] = useState("")
    const [notiMessage, setNotiMessage] = useState("")

    useEffect(() => {
        fetchPricePolicyList()
    }, [])

    const fetchPricePolicyList = async () => {
        let pricePolicy = await PricePolicyService.getPricePolicyList()
        setPolicyPriceList(pricePolicy.data)
    }

    const delPolicyPrice = (id) => {
        return PricePolicyService.delPricePolicy(id)
    }

    const handleDelPolicyPrice = (policyPriceId) => {
        setDelPolicyPriceId(policyPriceId)
        setConfirmMessage("Bn c mun xa dch v ny?")
    }

    const answer = (answer) => {
        if (answer) {
            delPolicyPrice(delPolicyPriceId).then(() => {
                setDelPolicyPriceId(null)
                setConfirmMessage("")
                setNotiMessage("Bn  cp nht thnh cng!")

                //update policyPriceList state after delete
                let newPolicyPriceList = [...policyPriceList]
                let delPolicyPriceIdx = newPolicyPriceList.findIndex((e) => e.id == delPolicyPriceId)
                newPolicyPriceList.splice(delPolicyPriceIdx, 1)
                setPolicyPriceList(newPolicyPriceList)
            }).catch((err) => {
                setNotiMessage("C li xy ra, vui lng th li!")
                console.log(err)
            })

        } else {
            setConfirmMessage("")
        }
    }

    const done = () => {
        setNotiMessage("")
    }

    return (
        <Fragment>
            <div className="container">
                <ModalConfirm message={confirmMessage} answer={answer}></ModalConfirm>
                <ModalNoti message={notiMessage} done={done}></ModalNoti>
                <h2 className="py-3">Qun l chnh sch gi</h2>
                <Link to="/app/service/price-policy/0">
                    <Button color="success" className="mb-3">Thm mi chnh sch gi</Button>
                </Link>
                <PolicyPriceTable policyPriceList={policyPriceList} handleDelPolicyPrice={handleDelPolicyPrice}></PolicyPriceTable>
            </div>

        </Fragment>
    );
}

const PolicyPriceTable = (props) => {
    let { policyPriceList, handleDelPolicyPrice } = props

    return (
        <Table hover bordered>
            <thead>
                <tr>
                    <th>STT</th>
                    <th>M chnh sch</th>
                    <th>Tn chnh sch</th>
                    <th>Gim gi</th>
                    <th>Thao tc</th>
                </tr>
            </thead>
            <tbody>
                {policyPriceList.map((item, index) => (
                    <tr key={item.id}>
                        <th scope="row">{index + 1}</th>
                        <td>{item.code}</td>
                        <td>{item.name}</td>
                        <td>{item.discount * 100} %</td>
                        <td>
                            <Link to={`/app/service/price-policy/${item.id}`}>
                                <Button color="primary" className="mr-2">Sa</Button>
                            </Link>
                            <Button color="danger" onClick={() => { handleDelPolicyPrice(item.id) }}>Xa</Button>
                        </td>
                    </tr>
                ))}
            </tbody>
        </Table>
    );
}

export default PolicyPriceManager;

