import React, { Component, Fragment } from 'react';
import { Table, Modal, ModalHeader, ModalBody, ModalFooter, Input, Label, Col, Row } from 'reactstrap';
import { SERVICE_TYPE, STEP_TYPE, ORDER_TYPE, STATUS, ModalNoti, Util  } from '../../Shared';
class TransTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            reason: ''
        }     
    }
    render() {
        let { isManager, items, customerCode, amountRetail } = this.props;
        let totalExam = 0;
        let totalTest = 0;
        let totalXray = 0;
        let totalEnt = 0;
        let totalUltrasound = 0;
        let totalOther = 0;
        let totalBuy = 0; 
        let total
        let item = items.filter(i => i.code?.includes(`${customerCode}`))
        .map((i, index) => {
            i.total = i.exam + i.test + i.xray + i.ent + i.ultrasound + i.buy;
            totalExam +=  +i.exam;
            totalTest +=  +i.test;
            totalXray +=  +i.xray;
            totalEnt +=  +i.ent;
            totalUltrasound += +i.ultrasound;
            totalOther += +i.other;
            totalBuy += +i.buy;
            total = totalExam + totalTest + totalXray + totalEnt + totalUltrasound + totalOther + totalBuy;
            return  <tr key={index}>
                        <td> {index+1} </td>
                        <td> {i.name} </td>
                        <td> {i.code} </td>
                        <td> {i.phone} </td>
                        <td> {i.address} </td>
                        <td> {i.exam !== 0 ? Util.formatPrice(i.exam) : ''} </td>
                        <td> {i.test !== 0 ? Util.formatPrice(i.test) : ''} </td>
                        <td> {i.xray !== 0 ? Util.formatPrice(i.xray) : ''} </td>
                        <td> {i.ent !== 0 ? Util.formatPrice(i.ent) : ''} </td>
                        <td> {i.ultrasound !== 0 ? Util.formatPrice(i.ultrasound) : ''} </td>
                        <td> {i.other !== 0 ? Util.formatPrice(i.other) : ''} </td>
                        <td> {i.buy !== 0 ? Util.formatPrice(i.buy) : ''} </td>
                        <td> {Util.formatPrice(i.total)} </td>
                        {isManager? <td className="transaction-btn-cancel middle pointer" onClick={e => this.props.searchCustomer(e, i.id)}>Hy</td> : null} 
                    </tr>
        })
        return (
            <Fragment>
                 <Table>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Bnh nhn</th>
                            <th>M bnh nhn</th>
                            <th>in thoi</th>
                            <th>a ch</th>
                            <th>Khm</th>
                            <th>Xt nghim</th>
                            <th>X quang</th>
                            <th>Ni soi</th>
                            <th>Siu m</th>
                            <th>Cp cu</th>
                            <th>Thuc</th>
                            <th>Tng</th>
                            {isManager? <th>Options</th> : null} 
                        </tr>
                    </thead>
                    <tbody>
                        {item}
                        {amountRetail > 0 ?
                        <tr> 
                            <td>{items.length}</td>
                            <td>KHCH L</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td>{Util.formatPrice(amountRetail)}</td>
                            <td>{Util.formatPrice(amountRetail)}</td>
                            { isManager ? <td></td> : null }
                        </tr>
                        : null}
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>Tng: </th>
                            <th>{Util.formatPrice(totalExam)}</th>
                            <th>{Util.formatPrice(totalTest)}</th>
                            <th>{Util.formatPrice(totalXray)}</th>
                            <th>{Util.formatPrice(totalEnt)}</th>
                            <th>{Util.formatPrice(totalUltrasound)}</th>
                            <th>{Util.formatPrice(totalOther)}</th>
                            <th>{Util.formatPrice(totalBuy + amountRetail)}</th>
                            <th>{Util.formatPrice(total + amountRetail)}</th>
                            {isManager? <th></th> : null} 
                        </tr>
                    </tbody>
                </Table>
            </Fragment>
        );
    }
}

export default TransTable;