import React from "react";
import { Row, Col, TabContent, TabPane, Nav, NavItem, NavLink, } from "reactstrap";
import { Form } from "../../../../Shared";
import Diagnostic from "./Diagnostic/Diagnostic";
import PreExamination from "./PreExamination/PreExamination";
import Prescriptions from "./Prescriptions/Prescriptions";
import DoctorServices from "../../Shared/DoctorService";
import CustomerForm from "../../../../Shared/Components/CustomerForm/CustomerForm";
import PatientList from "../../../../Shared/Components/PatientList/PatientList";
import Diagnosis from "./Diagnosis/Diagnosis";
import { withRouter } from "react-router";
import { STATUS, SERVICE_TYPE, LOCATION_TYPE } from "../../../../Constances/const";
import { Microscope } from "../../../../Shared";
import classnames from 'classnames';
class JobDoctor extends Form {
  constructor(props) {
    super(props);
    this.state = {
      servicesList: [],
      ProductList: [],
      patientList: [],
      selectedJobStep: {},
      selectedJob: {},
      textDiagnosis: "",
      activeTab: '1',
      titleWaitForExam: "Danh Sch Bnh Nhn Ch",
      navActiveListPeople: false,
      navActiveExam: false,
      notiQuantity: 0,
      resultList: []
    };
  }
  componentDidMount() {
    let promises = [
      DoctorServices.getListServices(),
      DoctorServices.getListProduct(),
    ];
    Promise.all(promises).then(([serviceList, medList]) => {
      let data = serviceList.data
        .filter(el => el.type === SERVICE_TYPE.TEST || el.type === SERVICE_TYPE.ENT || el.type === SERVICE_TYPE.ULTRASOUND || el.type === SERVICE_TYPE.XRAY || el.type === SERVICE_TYPE.OTHER);
      this.setState({
        servicesList: data,
        ProductList: medList.data,
      });
    }).catch(function (error) {
      console.log(error);
    });
    document.addEventListener("mousedown", this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener("mousedown", this.handleClickOutside);
  }

  onSelectJobStep = async (jobStep) => {
    DoctorServices.getJobById({ id: jobStep.job_id }).then(res => {
      res.state = res.state || {};
      res.state.subclinical = res.state.subclinical || {};
      res.state.textDiagnosis = res.state.textDiagnosis || '';
      this.setState({
        selectedJobStep: jobStep,
        selectedJob: res,
      });
    }).catch(err => {
      console.log(err);
    })
  };
  selectTypeCustomer = (tab) => {
    let { activeTab } = this.state
    if (activeTab !== tab) this.setState({ activeTab: tab })
  }
  handleReviewExamResult = (ser_id) => {
  }

  jobListAction = {
    refresh: null,
  }
  setNotiQuantity = (num) => {
    this.setState({
      notiQuantity: num
    })
  }
  shortKeyShowListPeople = (e) => {
    if (e.ctrlKey && e.key === "l") {
      e.preventDefault();
      this.setState({ navActiveListPeople: true, })
    }
  }

  myRef = React.createRef();
  handleClickOutside = e => {
    if (!this.myRef.current.contains(e.target)) {
      this.setState({ navActiveListPeople: false });
    }
  };
  hiddenPatientList = () => {
    this.setState({ navActiveListPeople: false });
  }
  onChangetextDiagnosis = (ev) => {
    this.setState({
      textDiagnosis: ev
    })
  }
  render() {
    // console.log(DoctorServices.location, 986543);
    let locationType = DoctorServices.location.type;
    let {
      notiQuantity,
      servicesList,
      ProductList,
      selectedJobStep,
      selectedJob,
      activeTab,
    } = this.state;
    let cusData = selectedJobStep.order ? selectedJobStep.order.customer : {};
    let location_id = selectedJobStep.location ? selectedJobStep.location.id : "";
    return (
      <div className="JobDoctor" onKeyDown={(e) => this.shortKeyShowListPeople(e)} tabIndex="0">
        <Row className="title-card">
          <Col>
            <span>{DoctorServices.location?.name}</span>
          </Col>
          <Col className="end">
            <span className="select-office text-right" onClick={() => {
              DoctorServices.location = {};
              window.location.replace('/app/doctor')
            }}><b>Chuyn phng</b></span>
          </Col>
        </Row>
        <hr className="mb-0 mt-0" />
        <Row className="content">
          <Col>
            <Row className="mt-1 mb-15">
              <Col sm="7">
                <div className="customCard p-15 mr-15 content-left">
                  <CustomerForm data={cusData} mode="compact" />
                  <PreExamination job={selectedJob} />
                  <Diagnosis job={selectedJob} onChangetextDiagnosis={this.onChangetextDiagnosis} />
                </div>
              </Col>
              <Col sm="5">
                <Diagnostic
                  textDiagnosis={this.state.textDiagnosis}
                  resultList={this.state.resultList}
                  servicesList={servicesList}
                  cusData={cusData}
                  stepId={selectedJobStep.id}
                  job={selectedJob}
                  onRefresh={this.jobListAction.refresh}
                />
              </Col>
            </Row>
            <Row className="customCard p-15">
              <Prescriptions
                ProductList={ProductList}
                cusData={cusData}
                job={selectedJob}
                stepId={selectedJobStep.id}
                onRefresh={this.jobListAction.refresh}
              />
            </Row>
          </Col>
        </Row>
        <div className={this.state.navActiveListPeople || this.state.navActiveExam ? "nav-active" : "nav-disable"}>
          <div className="group-btn">
            {/* <button className="btn-circle btn-people mb-2"
              onClick={() => this.setState({ navActiveListPeople: true })}
            ><span className="material-icons">people</span></button> */}
            <p className="btn-circle btn-people mb-2 pointer"
              onClick={() => this.setState({ navActiveListPeople: true })}
            ><span className="material-icons">people</span><sup>{notiQuantity > 0 ? <div>{notiQuantity}</div> : null}</sup></p>
          </div>
          <div className="content">
            <div className={this.state.navActiveListPeople ? "display" : "none-display"}>
              <div ref={this.myRef}>
                {locationType === LOCATION_TYPE.EMERGENCY ?
                  <div>
                    <Nav tabs className="selectTabs">
                      <NavItem className="titleTabs">
                        <NavLink className={classnames({ active: activeTab === '1' })} onClick={() => { this.selectTypeCustomer('1'); }}><b className="title-card">Bnh nhn ch khm cp cu</b></NavLink>
                      </NavItem>
                      <NavItem className="titleTabs">
                        <NavLink className={classnames({ active: activeTab === '2' })} onClick={() => { this.selectTypeCustomer('2'); }}><b className="title-card">Bnh nhn ch cp cu</b></NavLink>
                      </NavItem>
                      <NavItem className="titleTabs">
                        <NavLink className={classnames({ active: activeTab === '3' })} onClick={() => { this.selectTypeCustomer('3'); }}><b className="title-card">Bnh nhn  hon thnh</b></NavLink>
                      </NavItem>
                    </Nav>
                    <TabContent activeTab={activeTab} >
                      <TabPane tabId="1" className="customCard">
                        <PatientList
                          hiddenPatientList={this.hiddenPatientList}
                          titleOfList={this.state.titleWaitForExam}
                          action={this.jobListAction}
                          onSelectJobStep={(jobStep) => this.onSelectJobStep(jobStep)}
                          location_id={DoctorServices.location.id}
                          status={[STATUS.READY, STATUS.RUNNING]}
                          type={SERVICE_TYPE.EXAM}
                          setNotiQuantity={this.setNotiQuantity}
                          resultList={this.state.resultList}
                          hasNoti={true}
                          mode="doctor" />
                      </TabPane>
                      <TabPane tabId="2" className="customCard">
                        <PatientList
                          hiddenPatientList={this.hiddenPatientList}
                          titleOfList={this.state.titleWaitForExam}
                          action={this.jobListAction}
                          onSelectJobStep={(jobStep) => this.onSelectJobStep(jobStep)}
                          location_id={DoctorServices.location.id}
                          status={[STATUS.READY, STATUS.RUNNING]}
                          type={SERVICE_TYPE.TEST}
                          mode="doctor" />
                      </TabPane>
                      <TabPane tabId="3" className="customCard">
                        <PatientList
                          hiddenPatientList={this.hiddenPatientList}
                          titleOfList={this.state.titleWaitForExam}
                          action={this.jobListAction}
                          onSelectJobStep={(jobStep) => this.onSelectJobStep(jobStep)}
                          location_id={DoctorServices.location.id}
                          status={[STATUS.DONE]}
                          type={SERVICE_TYPE.TEST}
                          mode="doctor" />
                      </TabPane>
                    </TabContent>

                  </div>
                  :
                  <div>
                    <Nav tabs className="selectTabs">
                      <NavItem className="titleTabs">
                        <NavLink className={classnames({ active: activeTab === '1' })} onClick={() => { this.selectTypeCustomer('1'); }}><b className="title-card">Bnh nhn ch khm</b></NavLink>
                      </NavItem>
                      <NavItem className="titleTabs">
                        <NavLink className={classnames({ active: activeTab === '2' })} onClick={() => { this.selectTypeCustomer('2'); }}><b className="title-card">Bnh nhn  hon thnh</b></NavLink>
                      </NavItem>
                    </Nav>
                    <TabContent activeTab={activeTab} >
                      <TabPane tabId="1" className="customCard">
                        <PatientList
                          hiddenPatientList={this.hiddenPatientList}
                          titleOfList={this.state.titleWaitForExam}
                          action={this.jobListAction}
                          onSelectJobStep={(jobStep) => this.onSelectJobStep(jobStep)}
                          location_id={DoctorServices.location.id}
                          status={[STATUS.READY, STATUS.RUNNING]}
                          type={SERVICE_TYPE.EXAM}
                          setNotiQuantity={this.setNotiQuantity}
                          resultList={this.state.resultList}
                          hasNoti={true}
                          mode="doctor" />
                      </TabPane>
                      <TabPane tabId="2" className="customCard">
                        <PatientList
                          hiddenPatientList={this.hiddenPatientList}
                          titleOfList={this.state.titleWaitForExam}
                          action={this.jobListAction}
                          onSelectJobStep={(jobStep) => this.onSelectJobStep(jobStep)}
                          location_id={DoctorServices.location.id}
                          status={[STATUS.DONE]}
                          type={SERVICE_TYPE.EXAM}
                          mode="doctor" />
                      </TabPane>
                    </TabContent>
                  </div>       
                 
                }

              </div>
              <div className="doctor-opacity"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(JobDoctor);
