import React, { Component } from 'react';
import { Table, Row, Col } from 'reactstrap';
import RowTable from './RowTable';

class TableUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            confirmMessage: '',
            notiMessage: '',
        }
    }

    render() {
        let { userList } = this.props;
        let user = userList.map((user, index) => {
            return <RowTable key={user.id} user={user} index={index}
                onDelete={this.props.onDelete}
            ></RowTable>
        })
        return (
            <div className="table-responsive min-h-100 df-h-75">
                <table className="table table-head-fixed table-bordered">
                    <thead>
                        <tr>
                            <th className="dw-5">STT</th>
                            <th>Tn ng Nhp</th>
                            <th>Tn Ngi dng</th>
                            <th >Vai tr</th>
                            <th>Ngy Bt u</th>
                            <th>S in Thoi</th>
                            <th>Ngy sinh</th>
                            <th>Ty Chnh</th>
                        </tr>
                    </thead>
                    <tbody className="bodyTable">
                        {user}
                    </tbody>
                </table>
            </div>
        );
    }
}
export default TableUser
