import React, { useEffect, useState } from 'react';
import { Button, Table, Modal, ModalHeader, ModalBody, ModalFooter, } from 'reactstrap';
import { DATE } from "../../../../Constances/const";
import { ShareService } from '../../../../Shared';
import PrintMedicalHistory from "./PrintHistory/PrintMedicalHistory";
const MedicalHistoryTable = (props) => {
  let { items } = props;
  const [itemPrint, setItemPrint] = useState({});
  // const [submidPrint, setSunmitPrint] = useState(0);
  const [showPrint, setShowPrint] = useState(false);
  useEffect(() => {
  }, [items])
  const printMedicalHistory = (item) => {
    setItemPrint(item)
    setShowPrint(true);
  }
  const submitPrint = () => {
    ShareService.printhorizontal("historyMedicalPrint")
  }
  return (
    <div className="MedicalHistoryTable">
      <Table hover bordered className="historyMedical">
        <thead>
          <tr>
            <th>STT</th>
            <th className="nameCm">Tn bnh nhn</th>
            <th className="testDay">Ngy khm</th>
            <th>Dch v</th>
            <th>M dch v</th>
            <th className="optionHistory">Ty chn</th>
          </tr>
        </thead>
        <tbody>

          {items?.map((item, index) => {
            const stepExam = item.steps;

            const service = (sv) => {

              if (sv.order.items.length > 0) {
                let serviceList = [sv.order.items.map(el => el.ref_value.name).join(",")]

                return <div>{serviceList}</div>
              }
            }
            const code = (sv) => {
              if (sv.order) {
                let codeList = [sv.order.code]

                return <div key={codeList}>{codeList}</div>
              }
            }
            const codeExam = stepExam.map(code)
            const serviceExam = stepExam.map(service)
            return <tr key={item.id}>
              <th scope="row">{index + 1}</th>
              <td>{item.ref_value.full_name}</td>
              <td>{DATE(item.date, "DDMMYYYY")}</td>
              <td>{serviceExam}</td>
              <td >{codeExam}</td>
              <td><Button onClick={() => printMedicalHistory(item)}>in</Button></td>
            </tr>
          })}
        </tbody>
      </Table>
      <Modal isOpen={showPrint}>
        <ModalBody>
          <PrintMedicalHistory item={itemPrint}></PrintMedicalHistory>
        </ModalBody>
        <ModalFooter>
          <Button color="danger" onClick={() => setShowPrint(false)}>Hy</Button>
          <Button onClick={() => submitPrint()}>In</Button>
        </ModalFooter>
      </Modal>

    </div>
  );
}

export default MedicalHistoryTable;