import React, { Component } from "react";
import { Table, Col, Button, Input, Label, Row } from "reactstrap";
import ItemLot from './ItemLot';
import { Link } from "react-router-dom";
import InventoryService from "../Shared/InventoryService";
import ModalConfirm from '../../../../Shared/Components/ModalConfirm/ModalConfirm';

class ListLot extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      listWareHouse: [],
      listLot: [],
      listProduct: [],
      notiConfirm: '',
      textSearch: ''
    };
  }

  componentDidMount() {
    this.getData();
  }

  // list data location
  getData() {

    let promiseArr = [];
    promiseArr.push(InventoryService.allLot());
    promiseArr.push(InventoryService.listWareHouse());
    promiseArr.push(InventoryService.listProduct());
    Promise.all(promiseArr).then(([list_lot, list_warehouse, list_product]) => {
      let listLot = list_lot.data;
      let listWareHouse = list_warehouse.data;
      let listProduct = list_product.data;

      listLot.map(lo => {
        let foundP = listProduct.find(p => {
          return lo.product_id === p.id
        })
        lo.product_name = foundP ? foundP.name : 'NA';
        let foundWH = listWareHouse.find(wh => {
          return lo.warehouse_id === wh.id
        })
        lo.warehouse_name = foundWH ? foundWH.name : 'NA';
        return lo;
      })

      this.setState({
        listLot
      });
    })
    .catch((err) => {
      this.setState({
        notiMessage: 'Li vui lng bn th li sau !!'
      })
      console.log(err);
    });
  }

  onDelete = (id) => {
    this.setState({
      notiConfirm: 'Bn c chc chn mun xa !!',
      id: id
    })
  };

  answer = (isYes) => {
    if (isYes) {
      let data = {
        id: this.state.id
      }
      InventoryService.deleteLot(data)
        .then(() => {
          this.setState({ notiConfirm: '' })
          this.getData();
          window.location.href = './inventory/lot'
        }).catch((err) => {
          this.setState({
            notiMessage: 'Li vui lng bn th li sau !!'
          })
          console.log(err);
        });
    } else {
      this.setState({ notiConfirm: '' })
    }
  }

  render() {
    let { listLot, textSearch } = this.state;
    let itemLot = listLot.filter(i => i.code?.toUpperCase()?.includes(`${textSearch.toUpperCase()}`) )
    .map((itemB, index) => {
      return <ItemLot index={index} listLot={itemB} key={index} onDelete={this.onDelete} />;
    });
    return (
      <Col xs={{ size: '10', offset: '1'}} className="customCard mt-3">
        <div >
          <h1 className="title-card-lg">Danh sch l</h1>
        </div>
        <div className="addNew">
         
        </div>
        <Row>
          <Label xs={1}>Tm kim</Label>
          <Col xs={2}>
            <Input placeholder="Nhp tn l" type="search" onChange={e => this.setState({ textSearch: e.target.value})} />
          </Col>
          <Col xs={{ size: '2', offset: '7'}} className="end">
            <Link to={`./lot/0`}>
              <Button outline color="primary">Thm mi</Button>
            </Link>
          </Col>
        </Row>
        <div className="tableManager pt-10">
          <Table className="managerTable" hover striped bordered>
            <thead>
              <tr>
                <th >STT</th>
                <th >S l</th>
                <th>Product</th>
                <th>Kho</th>
                <th>S lng</th>
                <th>Tn kho</th>
                <th>NSX</th>
                <th>HSD</th>
                <th>Ngy to</th>
              </tr>
            </thead>
            <tbody className="bodyTable">
            {itemLot}
            </tbody>
          </Table>
        </div>
        <ModalConfirm message={this.state.notiConfirm} answer={this.answer}></ModalConfirm>
      </Col>
    );
  }
}

export default ListLot;
