import React, { Component } from "react";
import { Button } from "reactstrap";
import { Fragment, Row } from "react";
import { ROLE, DATE} from "../../../../../../Constances/const";
import { AuthService } from '../../../../Shared';

class RowTable extends Component {
  onDelete = (id) => {
    id = this.props.user.id;
    this.props.onDelete(id);
  };
  edit = (id) => {
    let url = "/app/user/" + id;
    window.location.assign(url);
  };
  render() {
    let user = this.props.user;
    let { id, full_name, username, roles, phone, birthday } = this.props.user;
    const ctime = new Date(user.ctime).toLocaleDateString("en-gb" , { day : "2-digit", month : "2-digit", year : "numeric"}).split("/");
    const values = Object.values(ROLE).map(v => v);
    const role_name = roles.map(role => {
      return values.find(v => {
        if (v.value == role) {
          return v.name
        }
      })
    })

    return (
      <Fragment>
        <tr>
          <td className="dw-5" scope="row">
            {this.props.index + 1}
          </td>
          <td width="10%">{username}</td>
          <td>{full_name}</td>
          {/* <td>{ROLE[roleKey]?.name}</td> */}
          <td>{role_name?.map(key => key === role_name[role_name?.length - 1] ? key?.name : key?.name + " - ")} </td>
          <td>
            {`${ctime[0]}-${ctime[1]}-${ctime[2]}`}
          </td>
          <td>{phone}</td>
          <td>
            {DATE(birthday, "DDMMYYYY")}
          </td>
          <td width="15%">
            <Button
              outline
              color="primary"
              className="btn-sm"
              onClick={() => this.edit(user.id)}
            >
              <span>Sa</span>
            </Button>{" "}
            <Button
              color="danger"
              className="btn-sm"
              onClick={this.onDelete}
            >
              Xa
            </Button>{" "}
            {AuthService.isRole(ROLE.ADMIN.value) ?
              <Button
                outline
                color="primary"
                className="btn-sm"
                onClick={() => window.location.assign(`/app/user/setpassword/${id}`)}
              >
                i mt khu
              </Button>
              : null
            }
          </td>
        </tr>
      </Fragment>
    );
  }
}
export default RowTable;
