import React, { useState, useEffect } from 'react';
import { Fragment } from 'react';
import { Button, Form, FormGroup, Label, Input, Col, Row } from 'reactstrap';
import { Link, useParams } from "react-router-dom"
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm"
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import PricePolicyService from "../../Shared/PricePolicyService"


const PricePolicyForm = (props) => {
    let { id } = useParams();

    const [pricePolicy, setPricePolicy] = useState({
        code: "",
        name: "",
        discount: "",
        type: "xn",
    })

    const [confirmMessage, setConfirmMessage] = useState("")
    const [notiMessage, setNotiMessage] = useState("")

    const handleChange = (e) => {
        const { name, value } = e.target;
        setPricePolicy((pricePolicy) => ({ ...pricePolicy, [name]: value }));
    }

    useEffect(() => {
        if (id !== "0") {
            fetchPricePolicy();
        }
    }, [])

    const fetchPricePolicy = async () => {
        let fetchedPricePolicy = await PricePolicyService.getPricePolicyById(id)
        setPricePolicy(fetchedPricePolicy.data)
    }

    const editPricePolicy = () => {
        const payload = {...pricePolicy, discount: pricePolicy.discount/100}
        return PricePolicyService.editPricePolicy(payload)
    }
    const postPricePolicy = () => {
        const payload = {...pricePolicy, discount: pricePolicy.discount/100}
        return PricePolicyService.postPricePolicy(payload)
    }

    const handleSubmitForm = () => {
        if (id === "0") {
            postPricePolicy().then(() => {
                setNotiMessage("Thm mi chnh sch gi thnh cng!")
            }).catch((err) => {
                console.log(err)
                setNotiMessage("C li xy ra, vui lng th li!")
            })

        } else {
            //cap nhat
            setConfirmMessage("Bn ng  cp nht li chnh sch gi ?")
        }
    }

    const answer = (answer) => {
        if (answer) {
            editPricePolicy().then(() => {
                setConfirmMessage("")
                setNotiMessage("Bn  cp nht thnh cng!")
            }).catch((err) => {
                setNotiMessage("C li xy ra, vui lng th li!")
            })

        } else {
            setConfirmMessage("")
        }
    }

    const done = () => {
        setNotiMessage("")
    }

    return (

        <Fragment>
            <div className="container">
                <ModalConfirm message={confirmMessage} answer={answer}></ModalConfirm>
                <ModalNoti message={notiMessage} done={done}></ModalNoti>
                <h2 className="py-3">{id !== "0" ? "Chnh sa chnh sch gi" : "Thm mi chnh sch gi"}</h2>
                <Form>
                    <FormGroup row>
                        <Label for="name" sm={2}>Tn chnh sch</Label>
                        <Col sm={10}>
                            <Input type="email" name="name" id="name" placeholder="Nhp tn chnh sch" value={pricePolicy.name} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Label for="code" sm={2}>M chnh sch</Label>
                        <Col sm={10}>
                            <Input type="text" name="code" id="code" placeholder="Nhp m chnh sch" value={pricePolicy.code} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Label for="discount" sm={2}>Phn trm gim gi</Label>
                        <Col sm={10}>
                            <Input type="number" name="discount" id="discount" placeholder="Nhp % gim gi" value={pricePolicy.discount} onChange={(e) => handleChange(e)} />
                        </Col>
                    </FormGroup>
                    <Row>
                        <Col sm={{ size: 10, offset: 2 }}>
                            <Link to="/app/service/price-policy">
                                <Button className="mr-2">Quay li</Button>
                            </Link>
                            <Button color="success" onClick={() => handleSubmitForm()}>{id !== "0" ? "Cp nht" : "Thm mi"}</Button>
                        </Col>
                    </Row>

                </Form>
            </div>
        </Fragment>
    );
}

export default PricePolicyForm;

