import React, { useState, Fragment } from 'react';
import { Button, FormGroup, Input, Col, Row } from 'reactstrap';
import ProductService from '../Shared/ProductService';

const FormProducer = () => {
  const [producer, setProducer] = useState({
    name: "",
    description: ""

  })

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProducer((producer) => ({ ...producer, [name]: value }));
  }
  const postProducer = () => {
    const payload = producer
    return ProductService.postProducer(payload)
  }


  return (
    <Fragment>
      <Row>
        <Col xs={2}></Col>
        <Col xs={8}>
          <FormGroup  name="producer">
            <h1 className="p-t-50">Nhp thng tin nh sn xut</h1>
            <Row>
              <Col xs="4">Tn nh sn xut</Col>
              <Col xs="8"> <Input type="input" name="name" id="name" placeholder="nhp tn nh sx" onChange={(e) => handleChange(e)} /></Col>
            </Row>
            <Row>
              <Col xs="4">Thng tin nh sn xut</Col>
              <Col xs="8"> <Input type="input" name="description" id="description" placeholder="Nhp thng tin nh sn xut" onChange={(e) => handleChange(e)} /></Col>
            </Row>
          </FormGroup>
          <Row>
            <Button color="success" className="btn_1" onClick={() => postProducer()}>Thm nh sn xut </Button>
          </Row>
        </Col>
        <Col xs={2}></Col>
      </Row>
    </Fragment>
  );
}

export default FormProducer;