import React from 'react';
import { Col, Row, Input } from 'reactstrap';
import DoctorServices from '../../../Shared/DoctorService'
import { Form } from '../../../../../Shared';
import { Util } from '../../../../../Helper/Util';
class PreExamination extends Form {
    constructor(props) {
        super(props);
        this.state = {
            blood: '',
            height: '',
            temp: '',
            weight: '',
            allergy: ''
        }
    }

    componentDidUpdate = (preProps) => {
        if (preProps.job.id !== this.props.job.id) {
            let blood = this.props.job.state.subclinical.blood || '';
            let height = this.props.job.state.subclinical.height || '';
            let temp = this.props.job.state.subclinical.temp || '';
            let weight = this.props.job.state.subclinical.weight || '';
            this.setState({
                blood,
                height,
                temp,
                weight,

            })
            let id = this.props.job.ref_id
            DoctorServices.getOneCustomer(id).then(res => {
                let allergy = res.data.allergy || '';
                this.setState({
                    allergy,
                })
            })
        }
    }

    onHandelChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    onBlurDiagnosis = () => {
        let { job } = this.props;
        let id = this.props.job.ref_id
        let subclinical = {
            blood: this.state.blood,
            temp: this.state.temp,
            weight: this.state.weight,
            height: this.state.height
        }
        DoctorServices.postSubclinical({
            id: job.id,
            state: {
                subclinical
            }
        }).then(res => {
            job.state = job.state || {};
            job.state.subclinical = subclinical;
            console.log('Thnh Cng', res);
        })
        let allergy = this.state.allergy;

        DoctorServices.postAllergyToCus(id, {
            allergy: allergy
        }).then(res => {
            console.log('Thnh Cng', res);
        })
    }

    render() {
        let { temp, height, weight, blood, allergy } = this.state
        return (
            <div className="PreExamination ">
                <Row>
                    <p className="title-card"><span className="material-icons">create</span> Du hiu sng</p>
                    <Col sm="7">
                        <Row className="info">
                            <Col sm={6}>
                                <Row className="mb-0">
                                    <Col sm={4}>
                                        <span>Nhit :</span>
                                    </Col>
                                    <Col className="m-0 p-0">
                                        <Input
                                            onKeyDown={(e) => Util.onKeyDown(e)}
                                            data-index="1"
                                            name="temp"
                                            value={temp}
                                            onChange={e => this.onHandelChange(e)}
                                            onBlur={() => { this.onBlurDiagnosis() }}
                                            disabled={!this.props.job.id}
                                        ></Input>
                                        <p className="m-1"> C</p>
                                    </Col>
                                </Row>
                            </Col>
                            <Col sm={6}>
                                <Row className="mb-0">
                                    <Col sm={4}>
                                        <span>Huyt p:</span>
                                    </Col>
                                    <Col className="m-0 p-0">
                                        <Input
                                            onKeyDown={(e) => Util.onKeyDown(e)}
                                            data-index="2"
                                            name="blood"
                                            value={blood}
                                            onChange={e => this.onHandelChange(e)}
                                            onBlur={() => { this.onBlurDiagnosis() }}
                                            disabled={!this.props.job.id}
                                        >
                                        </Input>
                                        <p className="m-1">mmHg</p>
                                        
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                        <Row className="info">
                            <Col sm={6}>
                                <Row className="mb-0">
                                    <Col sm={4}>
                                        <span>Cn nng:</span>
                                    </Col>
                                    <Col className="m-0 p-0">
                                        <Input name="weight"
                                            onKeyDown={(e) => Util.onKeyDown(e)}
                                            data-index="3"
                                            value={weight}
                                            onChange={e => this.onHandelChange(e)}
                                            onBlur={() => { this.onBlurDiagnosis() }}
                                            disabled={!this.props.job.id}
                                        ></Input>
                                        <p className="m-1">KG</p>
                                    </Col>
                                </Row>
                            </Col>
                            <Col sm={6}>
                                <Row className="mb-0">
                                    <Col sm={4}>
                                        <span>Chiu cao:</span>
                                    </Col>
                                    <Col className="m-0 p-0">
                                        <Input name="height"
                                            onKeyDown={(e) => Util.onKeyDown(e)}
                                            data-index="4"
                                            value={height}
                                            onChange={e => this.onHandelChange(e)}
                                            onBlur={() => { this.onBlurDiagnosis() }}
                                            disabled={!this.props.job.id}
                                        ></Input>
                                        <p className="m-1">CM</p>
                                        
                                    </Col>
                                   
                                </Row>
                            </Col>
                        </Row>
                    </Col>
                    <Col sm="5" className="">
                        <Row>
                            <Col sm="2">
                                <p className="text-danger"><b>D ng:</b></p>
                            </Col>
                            <Col sm="10">
                                <textarea
                                    className="form-control allergy"
                                    name="allergy"
                                    value={allergy}
                                    onChange={e => this.onHandelChange(e)}
                                    onBlur={this.onBlurDiagnosis}
                                    disabled={!this.props.job.id}
                                ></textarea>
                            </Col>

                        </Row>

                    </Col>
                </Row>
            </div>
        );
    }
}
export default PreExamination;