import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import { BrowserRouter as Link } from 'react-router-dom';
import ModalConfirm from '../../../../../../Shared/Components/ModalConfirm/ModalConfirm';
import ModalNoti from '../../../../../../Shared/Components/ModalNoti/ModalNoti';
import UserService from '../../../../Shared/UserService';
import { EQUAL_ARRAY, ROLE } from '../../../../../../Constances/const';

export default class AddUpdateUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      org_id: '',
      full_name: '',
      roles: [],
      phone: '',
      birthday: '',
      gender : '',

      listOrg: [],
      listRole: [],
      setRole: [],
      confirmMessage: ''
    }
  }

  componentDidMount() {
    let id = this.props.match.params.id;
    ////////// get user ////////////
    if (id !== 'create') {
      UserService.getEditUser(id)
        .then(response => {
          let { full_name, username, org_id, phone, birthday, roles, gender } = response.data
          let setRole = roles.map(el => el);
          this.setState({
            full_name, username, org_id, phone, roles, birthday, gender, roles, setRole
          });

        }).catch(error => {
          console.log(error);
        });
    }
    ////////// get list org ///////////
    UserService.getListOrg()
      .then(response => {
        this.setState({
          listOrg: response.data,
          org_id: response.data[0].id
        })
      }).catch(function (error) {
        console.log(error);
      })
  }

  onHandleChange = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    this.setState({
      [name]: value
    });
  }

  onHandleCheckBox = (e) => {
    if (e.target.checked == true) {
      this.state.setRole.push(e.target.value);
    } else {
      this.state.setRole.splice(this.state.setRole.indexOf(e.target.value), 1);
    }
  }

  onSubmit = () => {
    let id = this.props.match.params.id;
    if (id !== 'create') {
      this.setState({
        confirmMessage: 'Bn mun cp nhp thng tin ny khng ?'
      });
    } else {
      this.setState({
        confirmMessage: 'Bn mun thm mi thnh vin ny khng ?'
      });
    }
  }

  onSelectGender = (e) => {
    let gender = e.target.value;
    this.setState({ gender });
  } 

  answer = (answer) => {
    console.log(this.state.gender, "92")
    let id = this.props.match.params.id;
    let { full_name, username, phone, org_id, birthday, gender, roles, setRole } = this.state;
    let obj = {};
    if (EQUAL_ARRAY(setRole.sort(), roles.sort())) {
      obj = { full_name, username, phone, org_id, birthday, gender }
    } else {
      obj = { full_name, username, phone, org_id, birthday, gender, roles: setRole }
    }
    if (answer) {
      let method = (id !== 'create') ? UserService.postEditUser(id, obj) : UserService.postCreateUser(obj);
      let notiMessage = (id !== 'create') ? 'Cp nht thnh cng' : 'To mi thnh cng'
      method.then(response => {
        if (response.status === 200) {
          this.setState({
            notiMessage
          });
        }
      });
    } else {
      this.setState({
        confirmMessage: ''
      })
    }
  }

  doneAlret = () => {
    if (this.state.notiMessage) {
      window.location.replace('/app/user')
    } else {
      this.setState({ notiMessage: '' })
    }
  }

  onCancel = () => {
    window.location.assign('/app/user')
  }


  render() {
    let { full_name, username, roles, phone, org_id, birthday, listOrg , gender} = this.state
    let id = this.props.match.params.id;
    return (
      <div className="addUpdate ">
        <h4 className="text-center title-card-lg">
          {id !== 'create' ? "Cp nhp thng tin thnh vin" : "To mi nhn vin"}
        </h4>
        <Form
          style={{ width: "700px", margin: "40px auto" }}
          onSubmit={this.onHandleSubmit}
        >
          <Row form>
            <Col>
              <FormGroup sm={6}>
                <Label for="first_name">H Tn</Label>
                <Input
                  type="text"
                  className="full_name"
                  name="full_name"
                  value={full_name}
                  onChange={(e) => this.onHandleChange(e)}
                />
              </FormGroup>
            </Col>
          </Row>
          <FormGroup>
            <Label for="username">Tn ti khon</Label>
            <Input
              readOnly={id !== 'create' ? true : false}
              type="text"
              className="username"
              name="username"
              value={username}
              onChange={(e) => this.onHandleChange(e)}
            />
          </FormGroup>
          {/* <FormGroup >
              <Label>Chc v</Label>
              <Input
                readOnly
                type="select"
                id="role"
                name="role"
                value={roles}
                onChange={(e) => this.onHandleChange(e)}
              >
                {Object.keys(ROLE).map((k, i) => (<option key={i} value={ROLE[k].value}>{ROLE[k].name}</option>))}
              </Input>
            </FormGroup> */}
          <FormGroup>
            <Label for="phone">S in thoi</Label>
            <Input
              type="text"
              className="phone"
              name="phone"
              value={phone}
              onChange={(e) => this.onHandleChange(e)}
            />
          </FormGroup>
          <FormGroup>
            <Label for="birthday">Ngy sinh</Label>
            <input
              type="date"
              className="birthday form-control"
              // min="1890-04-01"
              // max="2050-04-20"
              name="birthday"
              min="01-04-1890"
              max="01-04-2050"
              value={birthday}
              onChange={(e) => this.onHandleChange(e)}
              required="required"
            ></input>
          </FormGroup>
          <FormGroup>
            <Label for="birthday">Gii tnh</Label>
            <Input type="select" name="gender" id="gender" onChange={(e) => this.onSelectGender(e) } >
              <option value="">Chn gii tnh</option>
              <option value="male">Nam</option>
              <option value="female">N</option>
            </Input>
          </FormGroup>
          {this.state.roles.includes(ROLE.ADMIN.value) === false ?
            <FormGroup>
              <Label for="position">Vai tr</Label>
              <Row sm={4}>
                {Object.values(ROLE).map((k, i) => {
                  if (k.value !== ROLE.ADMIN.value) {
                    return (
                      <div>
                        <Col sm={12}>
                          <Label sm={9} className="mt-1" for="position">{k.name}</Label>
                          <Input
                            sm={3} className="mt-3"
                            key={i} type="checkbox"
                            value={k.value}
                            defaultChecked={roles.find(role => role == k.value ? true : false)}
                            onClick={(e) => this.onHandleCheckBox(e)}
                          // onClick={(e) => this.unCheck(e)}
                          />
                        </Col>
                      </div>
                    )
                  }
                })}
              </Row>
            </FormGroup>
            : null}


          <div className="addControl">
            <Button
              outline
              color="primary"
              onClick={this.onSubmit}
            >
              {id !== 'create' ? "Cp nhp" : "Thm mi"}
            </Button>{" "}
            <Link to="../user/">
              <Button
                color="danger"
                onClick={this.onCancel}
              >
                Hy
              </Button>
            </Link>
          </div>
        </Form>
        <ModalConfirm
          message={this.state.confirmMessage}
          answer={this.answer}
        ></ModalConfirm>
        <ModalNoti
          message={this.state.notiMessage}
          done={this.doneAlret}
        ></ModalNoti>
      </div>
    );
  }
}

