import React, { Component } from "react";
import { Button } from "reactstrap";
import { Link } from "react-router-dom";



class ItemLocation extends Component {

  onDelete = (id) => {
    this.props.onDelete(id);
  };

  render() {
    let { list_location, index } = this.props;
    return (
        <tr key={index}>
          <td>{index + 1}</td>
          <td>{list_location.location_type_name}</td>
          <td>{list_location.code}</td>
          <td>{list_location.name}</td>
          <td>
            <div className="btnControl">
              <Link to={`./location/${list_location.id}`} className="btn btn-primary btn-sm">
                Sa
              </Link>{' '}
              <Button className="btnDanger btn-sm" color="danger" onClick={() => this.onDelete(list_location.id)}>
                Xa
              </Button>
            </div>
          </td>
        </tr>
    );
  }
}

export default ItemLocation;
