const readThreeDigits = (number) => {
  const digitText = [
    "khng",
    "mt",
    "hai",
    "ba",
    "bn",
    "nm",
    "su",
    "by",
    "tm",
    "chn",
  ];
  let word = "";
  let numberDigits = Array.from(number.toString()).map(Number).reverse();

  if (numberDigits[2]) {
    word += digitText[numberDigits[2]] + " trm";
  }
  if (numberDigits[1]) {
    if (numberDigits[1] === 1) {
      word += " mi";
    } else {
      word += " " + digitText[numberDigits[1]] + " mi";
    }
  } else if (numberDigits[2] && numberDigits[0]) {
    word += " linh";
  }
  if (numberDigits[0]) {
    if (numberDigits[0] === 5 && numberDigits[1]) {
      word += " lm";
    } else if (
      numberDigits[0] === 1 &&
      numberDigits[1] &&
      numberDigits[1] !== 1
    ) {
      word += " mt";
    } else {
      word += " " + digitText[numberDigits[0]];
    }
  }
  return word;
};

export const moneyToWord = (money) => {
  if (!money) return 'Khng ng'
  let word = "";
  const unit = ["nghn", "triu", "t"];
  let moneyTemp = money;

  let i = 0;
  let threeDigitsNumberArr = [];
  while (moneyTemp > 0) {
    threeDigitsNumberArr.push(moneyTemp % 1000);
    moneyTemp = Math.floor(moneyTemp / 1000);
  }
  if (threeDigitsNumberArr[0]) {
    word += readThreeDigits(threeDigitsNumberArr[0]) + " ";
  }
  if (threeDigitsNumberArr[1]) {
    word = readThreeDigits(threeDigitsNumberArr[1]) + " nghn " + word;
  }
  if (threeDigitsNumberArr[2]) {
    word = readThreeDigits(threeDigitsNumberArr[2]) + " triu " + word;
  }
  if (threeDigitsNumberArr[3]) {
    word = readThreeDigits(threeDigitsNumberArr[3]) + " t " + word;
  }
  if (threeDigitsNumberArr[4]) {
    word = readThreeDigits(threeDigitsNumberArr[4]) + " nghn " + word;
  }
  if (threeDigitsNumberArr[5]) {
    word = readThreeDigits(threeDigitsNumberArr[5]) + " triu " + word;
  }

  if (!(money % 1000000000000)) {
    word += "t ";
  }
  if (threeDigitsNumberArr[6] !== undefined) {
    return "Money too much";
  }

  return word + "ng";
};

export function numberWithCommas(x) {
  if(x)
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  else
    return 0;
}

export function getAge(dateString) {
  let today = new Date();
  let birthDate = toDate(dateString);
  let age = today.getFullYear() - birthDate.getFullYear();
  let m = today.getMonth() - birthDate.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    age--;
  }
  if (age === 0) return diff_weeks(today, birthDate) + " tun tui";
  return age + " tui";
}

// convert dd/mm/yyyy string to date
function toDate(dateStr) {
  const [day, month, year] = dateStr.split("/");
  return new Date(year, month - 1, day);
}

// eg 12031999 -> 12/03/1999
export function autoAddSlashDate(rawDate) {
  let result = rawDate
  if (result[2] && result[2] !== '/') {
    result = result.slice(0, 2) + "/" + result.slice(2);
  }

  if (result[5] && result[5] !== '/') {
    result = result.slice(0, 5) + "/" + result.slice(5);
  }
  return result
}

// dd/mm/yyyy to yyyy-mm-dd
export function convertToNormDate(dateStr) {
  let dateArr = dateStr.split("/").reverse();
  let day = dateArr[0];
  let month = dateArr[1];
  if (day.length === 1) dateArr[0] = "0" + day;
  if (month.length === 1) dateArr[1] = "0" + month;
  return dateArr.join("-");
}
export function convertToStrDate(normDate) {
  if (!normDate) {
    return "";
  }
  let strDate = normDate.split("-").reverse().join("/");
  return strDate;
}

export function daysBetween(StartDate, EndDate) {
  // The number of milliseconds in all UTC days (no DST)
  const oneDay = 1000 * 60 * 60 * 24;

  // A day in UTC always lasts 24 hours (unlike in other time formats)
  const start = Date.UTC(EndDate.getFullYear(), EndDate.getMonth(), EndDate.getDate());
  const end = Date.UTC(StartDate.getFullYear(), StartDate.getMonth(), StartDate.getDate());

  // so it's safe to divide by 24 hours
  return (start - end) / oneDay;
}

export function stringToSlug(str) {
  // remove accents
  let from = "",
      to   = "aaaaaaaaaaaaaaaaaeeeeeeeeeeeduuuuuuuuuuuoooooooooooooooooiiiiiaeiiouuncyyyyy";
  for (let i=0, l=from.length ; i < l ; i++) {
    str = str.replace(RegExp(from[i], "gi"), to[i]);
  }

  str = str.toLowerCase()
        .trim()
        .replace(/[^a-z0-9\-]/g, '-')
        .replace(/-+/g, '-');

  return str;
}

function diff_weeks(dt2, dt1) {
  let diff = (dt2.getTime() - dt1.getTime()) / 1000;
  diff /= 60 * 60 * 24 * 7;
  return Math.abs(Math.round(diff));
}
