import React, { useState, useEffect, Fragment } from "react";
import { Link } from "react-router-dom";
import { Button, FormGroup, Label, Input, Col, Row } from "reactstrap";
import InventoryService from "../Shared/InventoryService";
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import ReactAutocomplete from "react-autocomplete";

const FormLot = () => {
  const [Lot, setLot] = useState({
    code: "",
    warehouse_id: "",
    product_id: "",
    total: "",
    man_date: "",
    exp_date: "",
  });
  const [refresh, setRefresh] = useState(true);
  const [value, setValue] = useState("");
  
  const [ProductList, setProductList] = useState([]);
  //////////////// Sn_8-4-2021
  const [WarehouseList, setWarehouseList] = useState([]);
  ////////////////
  const [confirmMessage, setConfirmMessage] = useState("");
  const [notiMessage, setNotiMessage] = useState("");
  useEffect(() => {
    fetchProductList();
    //////////////// Sn_8-4-2021
    fetchWarehouseList();
    ////////////////
  }, []);
  const handleChange = (e) => {
    const { name, value } = e.target;
    setLot((Lot) => ({ ...Lot, [name]: value }));
  };

  const fetchProductList = async () => {
    let fetchProductList = await InventoryService.listProduct();
    setProductList(fetchProductList.data);
  };
  ////////////////////////////////////////////////
  const fetchWarehouseList = async () => {
    let fetchWarehouseList = await InventoryService.listWareHouse();
    setWarehouseList(fetchWarehouseList.data);
  };
  //////////////////////////////////////////////
  const createLot = () => {
    const payload = Lot;
    return InventoryService.createLot(payload);
  };

  let Warehouse_list = WarehouseList.map((data, i) => {
    return (
      <option value={data.id} key={i}>
        {data.name}
      </option>
    );
  });
  ////////////////////
  const onChangeProduct = (e) => {
    setValue(e.target.value);
  }
  const onSelectProduct = (value, item) => {
    setValue(value)
    setLot({...Lot, product_id : item.id})
  }
  const handleSubmitForm = () => {
    if(!Lot.warehouse_id) {
      setNotiMessage('Bn cha chn nh kho!');
      setRefresh(false)
      return
    } else if(!Lot.code || !Lot.product_id || !Lot.total || !Lot.man_date || !Lot.exp_date) {
      setNotiMessage('Bn cha nhp  thng tin!');
      setRefresh(false)
    } else {
      createLot()
      .then(() => {
        setNotiMessage('Thm mi thnh cng!');
      })
      .catch((err) => {
        console.log(err);
        setNotiMessage('C li xy ra, vui lng th li!');
      });
    }
  };

  const done = () => {
    if(refresh) {
      setNotiMessage("");
      setLot((Lot) => ({ ...Lot, 
        code: "",
        warehouse_id: "",
        product_id: "",
        total: "",
        man_date: "",
        exp_date: "", 
      }));
      setValue("");
      setRefresh(true);
    }else {
      setNotiMessage("");
      setRefresh(true);
    }
    
  };
  return (
    <Fragment>
      <div className="FormLot">
        <Row>
          <ModalNoti message={notiMessage} done={done}></ModalNoti>
          <Col xs="1"></Col>
          <Col xs="10">
            <h1 className="title-card-lg">Thm L Mi</h1>
            <FormGroup name="form_item">
              <Row>
                <Col xs="5">
                  <Row>
                    <Col xs="4">
                      <Label>Tn kho</Label>
                    </Col>
                    <Col>
                      <Input
                        type="select"
                        id="warehouse_id"
                        name="warehouse_id"
                        value={Lot.warehouse_id}
                        onChange={(e) => handleChange(e)}
                      >
                        <option>Chn nh kho</option>
                        {Warehouse_list}
                      </Input>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="4">
                      <Label>Tn l</Label>
                    </Col>
                    <Col>
                      <Input
                        type="text"
                        id="code"
                        name="code"
                        value={Lot.code}
                        onChange={(e) => handleChange(e)}
                        placeholder="Nhp tn l"
                      ></Input>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="4">
                      <Label>Sn phm</Label>
                    </Col>
                    <Col className="auto-form-control">
                    <ReactAutocomplete       
                      items={ProductList}
                      shouldItemRender={(item, value) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1}
                      getItemValue={item => item.name}
                      renderMenu={ children => (
                        <div className= "menu">
                           {children.slice(0, 9)}
                        </div>
                    )}
                      renderItem={(item) =>
                        <div
                          key={item.id}
                        >
                          {item.name}
                        </div>
                      }
                      // value={Lot.product_id}
                      // onChange={e => setLot({...Lot, product_id : e.target.value})}
                      // onSelect={value => setLot({...Lot, product_id : value})}
                      value = {value}
                      onChange = { e => onChangeProduct(e)}
                      onSelect = { (value, item) => onSelectProduct(value, item)}
                    />
                    </Col>
                  </Row>               
                 
                </Col>
                <Col xs={{size: '5', offset: '2'}}>
                <Row>
                    <Col xs="4">
                      <Label>S lng</Label>
                    </Col>
                    <Col>
                      <Input
                        type="number"
                        id="total"
                        name="total"
                        value={Lot.total}
                        onChange={(e) => handleChange(e)}
                        placeholder="Nhp s lng"
                      ></Input>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="4">
                      <Label>Ngy nhp</Label>
                    </Col>
                    <Col>
                      <Input
                        type="date"
                        name="man_date"
                        id="man_date"
                        value={Lot.man_date}
                        onChange={(e) => handleChange(e)}
                      ></Input>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="4">
                      <Label>Ngy ht hn</Label>
                    </Col>
                    <Col>
                      <Input
                        type="date"
                        name="exp_date"
                        id="exp_date"
                        value={Lot.exp_date}
                        onChange={(e) => handleChange(e)}
                      ></Input>
                         
                    </Col>
                    <Col xs={12}>
                      <div className="btnControlLot">
                        <Button
                          outline
                          color="primary"
                          className="btnAddNew"
                          onClick={() => handleSubmitForm()}
                        >
                          Thm mi
                        </Button>
                          &nbsp; &nbsp;
                        <Link to="/app/inventory/lot">
                          <Button className="mr-2" color="danger">Quay li</Button>
                        </Link>
                      </div>
                    </Col>
                  </Row>
                </Col>
              </Row>
           
            </FormGroup>
          </Col>
          <Col xs="1"></Col>
        </Row>
      </div>
    </Fragment>
  );
};

export default FormLot;
