import React, { Component } from "react";
import { Table, Container, Button } from "reactstrap";
import ItemTransaction from './ItemTransaction';
import { Link } from "react-router-dom";
import InventoryService from "../Shared/InventoryService";
import ProductService from '../../../Product/Components/Shared/ProductService';
import ModalConfirm from '../../../../Shared/Components/ModalConfirm/ModalConfirm';


class ListTransaction extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      listTransaction: [],
      listWareHouse: [],
      listLot: [],
      notiConfirm: '',
    };
  }

  componentDidMount() {
    this.getData();
  }

  // list data location
  getData() {
    let promiseArr = [];
    promiseArr.push(InventoryService.listTransaction());
    promiseArr.push(InventoryService.allLot());
    promiseArr.push(InventoryService.listWareHouse());
    promiseArr.push(ProductService.listProduct());
    Promise.all(promiseArr).then(([list_transaction, list_lot, list_warehouse, list_product]) => {
      let listLot = list_lot.data;
      let listWareHouse = list_warehouse.data;
      let listTransaction = list_transaction.data;
      let listProduct = list_product.data;

      listTransaction.map(tr => {
        let foundB = listLot.find(b => {
          return tr.lot_id === b.id
        })
        tr.lot_code = foundB ? foundB.code : 'NA';
        let foundWH = listWareHouse.find(wh => {
          return tr.warehouse_id === wh.id
        })
        tr.warehouse_name = foundWH ? foundWH.name : 'NA';

        let foundP = listProduct.find(p => {
          return tr.product_id === p.id
        })
        tr.product_name = foundP ? foundP.name : 'NA';
        return tr;
      })

      this.setState({
        listTransaction
      });
    })
      .catch((err) => {
        this.setState({
          notiMessage: 'Li vui lng bn th li sau !!'
        })
        console.log(err);
      });
  }

  onDelete = (id) => {
    this.setState({
      notiConfirm: 'Bn c chc chn mun xa !!',
      id: id
    })
  };

  answer = (isYes) => {
    if (isYes) {
      let data = {
        id: this.state.id
      }
      InventoryService.deleteTransaction(data)
        .then(() => {
          this.setState({ notiConfirm: '' })
          this.getData();
          window.location.href = './transaction'
        }).catch((err) => {
          this.setState({
            notiMessage: 'Li vui lng bn th li sau !!'
          })
          console.log(err);
        });
    } else {
      this.setState({ notiConfirm: '' })
    }
  }

  render() {
    let { listTransaction } = this.state;
    let itemTransaction = listTransaction.map((itemT, index) => {
      return <ItemTransaction index={index} listTransaction={itemT} key={index} onDelete={this.onDelete} />;
    });
    return (
      <Container className="transaction customCard mt-3">
        <div>
          <h1 className="title-card-lg">Danh sch nhp xut</h1>
        </div>
        <div>
          <Link to={`./transaction/create`}>
            <Button className="btnAdd" outline color="primary">
              <span>Thm Mi</span>
            </Button>
          </Link>
        </div>
        <Table className="transactionTable" hover bordered striped>
          <thead>
            <tr>
              <th >STT</th>
              <th className="batchName" >S l</th>
              <th>sn phm</th>
              <th>Kho</th>
              <th className="amount">S lng</th>
              <th>Ngy to  </th>
            </tr>
          </thead>
          <tbody className="bodyTable">
             {itemTransaction}
          </tbody>
        </Table>
        <ModalConfirm message={this.state.notiConfirm} answer={this.answer}></ModalConfirm>
      </Container>
    );
  }
}

export default ListTransaction;
