import React, { Fragment } from 'react';
import logo from '../../../Asset/Img/logo.png';
import { withRouter } from "react-router-dom";
import { Nav, NavItem, Dropdown, DropdownItem, DropdownToggle, DropdownMenu, NavLink } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import { AuthService } from '../../';
import { EQUAL_ARRAY, ROLE } from '../../../Constances/const';


class AppHeader extends React.Component {
    state = {
        dropdownUserOpen: false,
        dropdownMasterDataOpen: false,
        dropdownInventoryOpen: false,
    }

    goTo(url = '') {
        url = window.location.origin + '/' + url
        window.location.replace(url)
    }

    toggleUser = () => {
        this.setState({
            dropdownUserOpen: !this.state.dropdownUserOpen,
        })
    }

    toggleMasterData = () => {
        this.setState({
            dropdownMasterDataOpen: !this.state.dropdownMasterDataOpen,
        })
    }

    toggleWareHouse = () => {
        this.setState({
            dropdownInventoryOpen: !this.state.dropdownInventoryOpen,
        })
    }

    logout() {
        AuthService.userInfo = null;
        window.sessionStorage.clear();
        window.location.replace('login');
    }
   
    render() {
        return (
            <Nav pills>
                <Row className="appHeaderContainer" >
                    <Col xs="1">
                        <NavItem className="mt-1">
                            <img src={logo} alt="img company" style={{ width: 60 }, { height: 30 }}></img>
                        </NavItem>
                    </Col>
                    
                    { EQUAL_ARRAY(AuthService.userInfo.roles, [ROLE.ADMIN.value]) === true ?
                        <Col sm="9">
                            <Row style={{ margin: '0px' }}>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/transaction/order')}>Thu Ngn</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/reception')}>L Tn</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/doctor')}>Khm Bnh</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/exam')}>Xt Nghim</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/xray')}>X-Quang</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/endoscopic')}>Ni soi</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/ultrasound')}>Siu m</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/pharmacy')}>Nh thuc</NavLink>
                                </NavItem>
                                <NavItem className="pointer">
                                    <NavLink onClick={() => this.goTo('app/transaction/transaction')}>Giao dch</NavLink>
                                </NavItem>
                                <Dropdown nav isOpen={this.state.dropdownInventoryOpen} toggle={this.toggleWareHouse}>
                                    <DropdownToggle nav caret>Kho</DropdownToggle>
                                    <DropdownMenu>
                                        <DropdownItem onClick={() => this.goTo('app/inventory/lot')}>Nhp L</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/inventory/transaction')}>Xut Kho</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/product')}>Thuc</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/product/part')}>Thnh phn</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/inventory/producer')}>Nh Cung Cp</DropdownItem>
                                    </DropdownMenu>

                                </Dropdown>
                                <Dropdown nav isOpen={this.state.dropdownMasterDataOpen} toggle={this.toggleMasterData}>
                                    <DropdownToggle nav caret>Qun l</DropdownToggle>
                                    <DropdownMenu>
                                        <DropdownItem onClick={() => this.goTo('app/user')}>Nhn Vin</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/location')}>Phng Ban</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/service')}>Dch V</DropdownItem>
                                        <DropdownItem onClick={() => this.goTo('app/transaction/statistical')}>Thng k bc s</DropdownItem>
                                    </DropdownMenu>
                                </Dropdown>
                            </Row>
                        </Col>
                         :  
                        <Col sm={9}>
                            <Row style={{ margin: '0px' }}>
                            {AuthService.userInfo.roles.map(role => {
                                if (role === ROLE.ACCOUNTER.value || role === ROLE.RECEPTIONIST.value) {
                                    return (
                                        <Fragment>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/transaction/order')}>Thu Ngn</NavLink>         
                                            </NavItem>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/reception')}>L Tn</NavLink>
                                            </NavItem>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/transaction/transaction')}>Giao dch</NavLink>
                                            </NavItem>
                                        </Fragment>
                                    )
                                // } if (role === ROLE.RECEPTIONIST.value) {
                                //     return (
                                //        <NavItem className="pointer">
                                //             <NavLink onClick={() => this.goTo('app/reception')}>L Tn</NavLink>
                                //         </NavItem>
                                //     )
                                } if (role === ROLE.DOCTOR.value) {
                                    return (
                                        <Fragment>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/doctor')}>Khm Bnh</NavLink>
                                            </NavItem>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/transaction/statistical')}>Thng k</NavLink>
                                            </NavItem>
                                        </Fragment>
                                    )
                                } if (role === ROLE.TEST_OPERATOR.value) {
                                    return (
                                        <NavItem className="pointer">
                                             <NavLink onClick={() => this.goTo('app/exam')}>Xt Nghim</NavLink>
                                        </NavItem>
                                    )
                                } if (role === ROLE.XRAY.value) {
                                    return (
                                        <NavItem className="pointer">
                                            <NavLink onClick={() => this.goTo('app/xray')}>X-Quang</NavLink>
                                        </NavItem>
                                    )
                                } if (role === ROLE.ENT.value) {
                                    return (
                                        <NavItem className="pointer">
                                            <NavLink onClick={() => this.goTo('app/endoscopic')}>Ni soi</NavLink>
                                        </NavItem>
                                    )
                                } if (role === ROLE.ULTRASOUND.value) {
                                    return (
                                        <NavItem className="pointer">
                                            <NavLink onClick={() => this.goTo('app/ultrasound')}>Siu m</NavLink>
                                        </NavItem>
                                    )
                                } if (role === ROLE.PHARMACIST.value) {
                                    return (
                                        <Fragment>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/pharmacy')}>Nh thuc</NavLink>
                                            </NavItem>
                                            <NavItem className="pointer">
                                                <NavLink onClick={() => this.goTo('app/transaction/transaction')}>Giao dch</NavLink>
                                            </NavItem>
                                            <Dropdown nav isOpen={this.state.dropdownInventoryOpen} toggle={this.toggleWareHouse}>
                                                <DropdownToggle nav caret>Kho</DropdownToggle>
                                                <DropdownMenu>       
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/lot')}>Nhp L</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/transaction')}>Xut Kho</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/product')}>Thuc</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/product/part')}>Thnh phn</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/producer')}>Nh Cung Cp</DropdownItem>
                                                </DropdownMenu>
                                            </Dropdown>
                                        </Fragment>
                                    )
                                }  if (role === ROLE.INVENTORY.value) {
                                    return (
                                        <Fragment>
                                            <Dropdown nav isOpen={this.state.dropdownInventoryOpen} toggle={this.toggleWareHouse}>
                                                <DropdownToggle nav caret>Kho</DropdownToggle>
                                                <DropdownMenu>
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/producer')}>Nh Cung Cp</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/lot')}>Nhp L</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/inventory/transaction')}>Xut Kho</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/product')}>Thuc</DropdownItem>
                                                    <DropdownItem onClick={() => this.goTo('app/product/part')}>Thnh phn</DropdownItem>
                                                </DropdownMenu>
                                            </Dropdown>
                                        </Fragment>
                                    )
                                }
                            })}
                            </Row>  
                        </Col>   
                    }
                    <Col sm="2">
                        <Dropdown nav isOpen={this.state.dropdownUserOpen} toggle={this.toggleUser}>
                            <DropdownToggle nav caret><span className="material-icons">account_box</span> {AuthService.userInfo.full_name}</DropdownToggle>
                            <DropdownMenu>
                                {/* <DropdownItem onClick={() => this.goTo('app/user/' + AuthService.userInfo.id)}><span className="material-icons">account_circle</span> Thng tin ngi dng</DropdownItem> */}
                                <DropdownItem onClick={() => this.goTo('app/user/setpassword/' + AuthService.userInfo.id)}><span className="material-icons">lock</span> i mt khu</DropdownItem>
                                <DropdownItem divider />
                                <DropdownItem onClick={() => this.logout()}><span className="material-icons">exit_to_app</span> ng xut</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                    </Col>
                </Row>
            </Nav>
        );
    }
}

export default withRouter(AppHeader);