import React, { useState, useEffect, Fragment } from 'react';
import { Button, FormGroup, Input, Col, Row } from 'reactstrap';
import ModalConfirm from "../../../../Shared/Components/ModalConfirm/ModalConfirm"
import ModalNoti from "../../../../Shared/Components/ModalNoti/ModalNoti"
import InventoryService from '../Shared/InventoryService';
import { Link, useParams } from "react-router-dom"

const FormProducer = () => {
  let { id } = useParams();
  const [producer, setProducer] = useState({
    name: "",
    description: ""

  })
  const [confirmMessage, setConfirmMessage] = useState("")
  const [notiMessage, setNotiMessage] = useState("")
  useEffect(() => {
    if (id !== "0") {
      fetchProducer();
    }
  }, [])

  const fetchProducer = async () => {
    let fetchProducer = await InventoryService.getProducerbyId(id)
    setProducer(fetchProducer.data)
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProducer((producer) => ({ ...producer, [name]: value }));
  }
  const editProducer = () => {
    const payload = producer
    return InventoryService.editProducer(payload)
  }
  const postProducer = () => {
    const payload = producer
    return InventoryService.postProducer(payload)
  }
  const handleSubmitForm = () => {
    if (id === "0") {
      postProducer().then(() => {
            setNotiMessage("Thm mi !")
        }).catch((err) => {
            console.log(err)
            setNotiMessage("C li xy ra, vui lng th li!")
        })

    } else {
        //cap nhat
        setConfirmMessage("Bn ng  cp nht ?")
    }
}
const answer = (answer) => {
  if (answer) {
    editProducer().then(() => {
          setConfirmMessage("")
          setNotiMessage("Bn  cp nht thnh cng!")
      }).catch(() => {
          setNotiMessage("C li xy ra, vui lng th li!")
      })

  } else {
      setConfirmMessage("")
  }
}

const done = () => {
  setNotiMessage("")
}

  return (
    <Fragment>
      <Row>
        <Col xs={2}></Col>
        <Col xs={8}>
        <ModalConfirm message={confirmMessage} answer={answer}></ModalConfirm>
        <ModalNoti message={notiMessage} done={done}></ModalNoti>
        <h2 className="title-card-lg">{id !== "0" ? "Chnh sa Nh Sn Xut" : "Thm mi Nh Sn Xut"}</h2>
          <FormGroup name="producer">
            <Row>
              <Col xs="4">Tn nh sn xut</Col>
              <Col xs="8"> <Input type="input" name="name" id="name" value={producer.name} placeholder="nhp tn nh sx" onChange={(e) => handleChange(e)} /></Col>
            </Row>
            <Row>
              <Col xs="4">Thng tin nh sn xut</Col>
              <Col xs="8"> <Input type="input" name="description" id="description" value={producer.description} placeholder="Nhp thng tin nh sn xut" onChange={(e) => handleChange(e)} /></Col>
            </Row>
          </FormGroup>
          <div className="producerCreateControl">
            <Button outline color="primary" onClick={() => handleSubmitForm()}>{id !== "0" ? "Cp nht" : "Thm mi"}</Button>
            <Link to="/app/inventory/producer">
              {" "}<Button className="mr-2" color="danger">Quay li</Button>
            </Link>
          </div>
        </Col>
        <Col xs={2}></Col>
      </Row>
    </Fragment>
  );
}

export default FormProducer;