import React, { Component } from 'react';
import LocationService from '../Shared/LocationService';
import ModalNoti from '../../../../Shared/Components/ModalNoti/ModalNoti'
import {
  Row,
  Col,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  Container,
} from 'reactstrap';


class ActionLocationType extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      code: '',
      nameRoom: '',
      typeRoom: '',
      notiMessage: '',
      dataType: [],
    };
  }



  componentDidMount() {
    let { match } = this.props;
    let id = match.params.id
    if (id) {
      LocationService.getUpdateLocationType(id)
        .then((response) => {
          let dataEdit = response.data;
          this.setState({
            id: dataEdit.id,
            nameRoom: dataEdit.name,
            code: dataEdit.code,
          });
        }).catch((error) => {
          console.log(error)
        });
    }
    return;
  }

  onBack = () => {
    window.history.back();
  }

  // data
  onchange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    },
    );
  };
  // create and update data
  onSubmit = () => {
    let { id, code, nameRoom } = this.state;
    if (id) {
      let data = {
        id: id,
        code: code,
        name: nameRoom,
      };
      LocationService.updateLocationType(data)
        .then(() => {
          this.setState({
            notiMessage: 'Cp nht thnh cng'
          })
        })
        .catch((err) => {
          this.setState({
            notiMessage: 'Li vui lng th li sau !!'
          })
          console.log(err);
        });
    } else {
      let data = {
        code: code,
        name: nameRoom,
      };
      LocationService.createLocationType(data)
        .then(() => {
          this.setState({
            notiMessage: 'Thm mi thnh cng'
          })
        }).catch((err) => {
            this.setState({
              notiMessage: 'Li vui lng th li sau !!'
            })
            console.log(err);
        });
    }
  };

  doneAlert = () => {
    if (this.state.notiMessage) {
      window.history.back();
    } else {
      this.setState({ notiMessage: '' })
    }
  }



  render() {
    let { id, code, nameRoom } = this.state;
    return (
      <section className="location">
        <Container>
          <Row>
            <Col xs={{ size: 8, offset: 2 }}>
              <div className="titleNameRoom">
                <h2>{id === '' ? 'Thm mi loi phng ban' : 'Cp nht loi phng ban'}</h2>
              </div>
              <Form className="location_form" id="location_form">
                <FormGroup>
                  <Label for="code">M loi phng</Label>
                  <Input
                    id="code"
                    type="text"
                    name="code"
                    value={code}
                    placeholder="Nhp m loi phng"
                    onChange={this.onchange}
                  />
                </FormGroup>
                <FormGroup>
                  <Label for="nameRoom">Tn loi phng</Label>
                  <Input
                    type="text"
                    name="nameRoom"
                    value={nameRoom}
                    placeholder="Nhp tn loi phng"
                    onChange={this.onchange}
                  />
                </FormGroup>
                <Button color="danger btnDanger" onClick={this.onBack} >
                  Hy B
                </Button>
                <Button color="success" onClick={this.onSubmit}>
                  {id === '' ? 'Thm mi' : 'Cp nht'}
                </Button>
              </Form>
            </Col>
          </Row>
        </Container>
        <ModalNoti message={this.state.notiMessage} done={this.doneAlert}></ModalNoti>
      </section>

    );
  }
}
export default ActionLocationType;
